<template>
<div class="conwarp">
<div class="grid">
    <div class="gmain">
      <h2>资金公司信息概况  &nbsp;  &nbsp; &nbsp; &nbsp; &nbsp;<el-button :span="2" type="primary" @click="toSavefound()">保存</el-button> </h2>
      <h5 >
        公司ID：<span >{{foundCompany.companyId}}</span>&nbsp;  &nbsp; &nbsp;  &nbsp; &nbsp; &nbsp; &nbsp;
        公司简称:<span>{{foundCompany.abbreviationName}}</span>&nbsp;  &nbsp; &nbsp;  &nbsp; &nbsp; &nbsp; &nbsp;

   <!--     审核状态:
        <span v-if="foundCompany.auditStatus==0">待资质认证</span>
        <span v-if="foundCompany.auditStatus==1">待审核资质</span>
        <span v-if="foundCompany.auditStatus==2" style="color:#67C23A">完成资质认证</span>
        <span v-if="foundCompany.auditStatus==3" style="color:#F56C6C">资质认证失败</span>-->
      </h5>

      <el-form :model="foundCompany" :rules="rules" ref="foundCompany" label-width="170px">
        <el-row>
          <el-col :span="12">
            <el-form-item label="接受资产类型：">
               <el-checkbox v-for="check in foundCheck" :label="check.itemName" :key="check.code" v-model="check.checked" @change="handleFoundCheckChange(check.code)"></el-checkbox>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="10">
            <el-form-item label="预测资产需求量：" prop="forecastAssetDemand">
                <el-input v-model="foundCompany.forecastAssetDemand" placeholder="金额"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="10">
            <el-form-item label="对资产的要求：" prop="foundCondition">
                <textarea v-model="foundCompany.foundCondition" style="width:98%; height:50px;" placeholder="0～100字符"></textarea>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="10">
            <el-form-item label="资金年化利率范围：" prop="foundYearRateRand">
                <el-input v-model="foundCompany.foundYearRateRand" placeholder="12%～18%"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="10">
            <el-form-item label="资金成本：" prop="foundCost">
                <el-input v-model="foundCompany.foundCost" placeholder="年化利率"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="10">
            <el-form-item label="月平均资金成交利率：" prop="monthTransactionRate">
                <el-input v-model="foundCompany.monthTransactionRate" placeholder="百分比"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="10">
            <el-form-item label="募集失败率：" prop="collectFailRate">
                <el-input v-model="foundCompany.collectFailRate" placeholder="百分比"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="10">
            <el-form-item label="公司业务区域：" prop="businessArea">
                <el-input v-model="foundCompany.businessArea" placeholder="城市，可选全国，多个城市用英文逗号分隔"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
</div>
</div>
</template>
<style scoped>

  h2{
    padding:0;
    text-align:left;
    border-bottom:1px solid #000;
    padding:0 0 6px 4px;
  }
  h5{
    text-align:left;
    padding:0 0 4px 14px;
    font-size:15px;
    color:#8c8c8c;
    border-bottom: solid 1px #eee;

  }
</style>
<script>
import {getFoundCompany,saveFoundCompany} from '@/api'
import {decimal_10_2,percent_scoop} from '@/libs/Regular'
import MomentPlugin from '@/libs/MomentPlugin'
export default {
  data () {
      return{
             foundCompany:{
               companyId:'',
               abbreviationName:'',
               id:'',
               auditStatus:'',
               assetInfo:'',
               forecastAssetDemand:'',
               foundCondition:'',
               foundYearRateRand:'',
               foundCost:'',
               monthTransactionRate:'',
               collectFailRate:'',
               businessArea:''
             },
             foundCheck:[
                     {code: '11', itemName: '融资租赁',checked:false},
                     {code: '12', itemName: '车抵贷',checked:false},
                     {code: '13', itemName: '房抵贷',checked:false},
                     {code: '14', itemName: '信用贷',checked:false},
                     {code: '15', itemName: '供应链金融',checked:false},
                     {code: '16', itemName: '其他',checked:false},
                     {code: '10', itemName: '消费分期',checked:false},
             ]
             ,
             subFoundCheck:[
                      {code: '1001', itemName: '3C消费',checked:false},
                      {code: '1002', itemName: '个人日常消费',checked:false},
                      {code: '1003', itemName: '装修',checked:false},
                      {code: '1004', itemName: '旅游',checked:false},
                      {code: '1005', itemName: '教育',checked:false},
                      {code: '1006', itemName: '医疗',checked:false},
                      {code: '1007', itemName: '其他',checked:false},
            ],
            rules:{
                foundCondition: [{max: 100, message: '长度在100个字符以内', trigger: 'blur' }],
                forecastAssetDemand: [{validator:decimal_10_2, trigger: 'blur' }],
                foundYearRateRand: [{validator:percent_scoop, trigger: 'blur' }],
                monthTransactionRate: [{validator:decimal_10_2, trigger: 'blur' }],
                foundCost: [{validator:decimal_10_2, trigger: 'blur' }],
                collectFailRate: [{validator:decimal_10_2, trigger: 'blur' }],
                businessArea: [{max: 100, message: '长度在100个字符以内', trigger: 'blur' }],
             },
             dialogWidth:'500px',
             formLabelWidth: '80px'

      };
  },
  mounted(){
     var self=this;
     self.loadData();
  },
  methods: {
    loadData: function () {
      var self=this;
      getFoundCompany({}).then(result => {
        var data = result.data;
        if(data == null){
            self.showDefault();
        }
        else{
          self.foundCompany = data;
          self.showDefault();
        }
      });
    },
    showDefault:function(){
      var self = this;

      if(self.foundCompany.assetInfo.length > 0){

        if(self.foundCompany.assetInfo.match("10") != null){
            self.foundCheck = self.foundCheck.concat(self.subFoundCheck);
        }
        var tempAreray = self.foundCheck;
        var arr = self.foundCompany.assetInfo.split(',');

          for(var i = 0; i < tempAreray.length; i++){
            var item1 = tempAreray[i];
            for(var j = 0; j < arr.length; j++){
              var item2 = arr[j];
              if(item1.code == item2){
                item1.checked = true;
              }
            }
        }
      }
    },
    toSavefound: function () {
       var self = this;
       var formName = 'foundCompany';
      self.$refs[formName].validate((valid) => {
        if(valid){
          var assetInfo = "";
          for(var i = 0; i < self.foundCheck.length; i++){
            var item1 = self.foundCheck[i];
              if(item1.checked == true){
                if(assetInfo.length == 0){
                  assetInfo += item1.code;
                }
                else{
                  assetInfo += "," + item1.code;
                }
              }
          }
          self.foundCompany.assetInfo = assetInfo;
          var params = self.foundCompany;

          saveFoundCompany(params).then(result => {
            var data = result.data;
            if(result.code!="200"){
                  this.$message.error(result.message);
                  return;
              }
            self.foundCompany.id = data;
            self.foundCompany.auditStatus=1;
            this.$message({type: 'success',message: '保存成功!'});
          });
        }

      });

    },
    handleFoundCheckChange: function(code){

        var tempAreray = this.subFoundCheck;

        for(var i = 0; i < this.foundCheck.length; i++){
            var item = this.foundCheck[i];
            if(item.code == code){
              if(code == '10'){
                if(item.checked == true){
                    this.foundCheck = this.foundCheck.concat(tempAreray);
                }
                else{
                    this.foundCheck.splice(7,7);
                    for(var j = 0; j < this.subFoundCheck.length; j++){
                        var item1 = this.subFoundCheck[j];
                        item1.checked = false;
                    }
                }
              }
              break;
            }
        }
    },
  }
}
</script>



