<template>
<div class="conwarp">
   <h2>公司信息认证  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span style="color: red">审核状态：</span>
     <span v-if="form.auditStatus==0" style="color: red">待资质认证</span>
     <span v-if="form.auditStatus==1" style="color: red">待审核资质</span>
     <span v-if="form.auditStatus==2" style="color: red">完成资质认证</span>
     <span v-if="form.auditStatus==3" style="color: red">资质认证失败</span>
   </h2>
  <el-form ref="form" :model="form" label-width="80px">
  <h5>公司基础信息   <el-button class="ed"  icon="el-icon-edit" circle @click="saveBaseInfo"></el-button></h5>
    <el-row>
      <el-col :span="6">
        <input type="hidden" v-model="form.id"/>
        <el-form-item label="名称">
          <el-input v-model="form.fullName"></el-input>
        </el-form-item>
        <el-form-item label="简称">
          <el-input v-model="form.abbreviationName"></el-input>
        </el-form-item>
        <el-form-item label="公司介绍">
          <textarea v-model="form.dsc" style="width:98%; height:50px;" placeholder="0～100字符"></textarea>
        </el-form-item>
        </el-col>
    </el-row>
    <h5>企业联系人信息   <el-button class="ed"  icon="el-icon-edit" circle @click="saveLinkInfo"></el-button></h5>
    <el-row>
      <el-col :span="6">
        <el-form-item label="联系人">
          <el-input v-model="form.linkMan"></el-input>
        </el-form-item>
        <el-form-item label="电话">
          <el-input v-model="form.mobile"></el-input>
        </el-form-item>
        <el-form-item label="邮箱">
          <el-input v-model="form.email"></el-input>
        </el-form-item>
        <el-form-item label="通信地址">
          <el-input v-model="form.address"></el-input>
        </el-form-item>
        </el-col>
    </el-row>
    <h5>企业资质信息   <el-button class="ed"  icon="el-icon-edit" circle @click="saveAuditInfo"></el-button></h5>
    <el-row>
      <el-col :span="6">
        <el-form-item label="企业法人身份证" label-width="120px" >
          <el-input v-model="form.idNumber"></el-input>
           <div class="imgbox">
             <img :src="form.idCardFirstUrl" class="img"/>
             <img :src="form.idCardSecondUrl" class="img"/>
           </div>
           <div class="filenox" >
             <input type="file"  name="file" @change="getFile($event,'idCardFirstUrl')" />
             <input type="file"  name="file" @change="getFile($event,'idCardSecondUrl')" />
           </div>
        </el-form-item>
        <el-form-item label="社会信用代码证" label-width="120px">
          <el-input v-model="form.unifyCreditNum"></el-input>
          <div class="imgbox">
            <img :src="form.unifyCreditUrl" class="img"/>
           </div>
           <div class="filenox" >
             <input type="file"  name="file" @change="getFile($event,'unifyCreditUrl')" />
           </div>
        </el-form-item>
        <el-form-item label="银行开户许可证" label-width="120px">
          <el-input v-model="form.openingBankNum"></el-input>
          <div class="imgbox">
            <img :src="form.openingBankUrl" class="img"/>
           </div>
           <div class="filenox" >
             <input type="file"  name="file" @change="getFile($event,'openingBankUrl')" />
           </div>
        </el-form-item>
        <el-form-item label="银行信用证" label-width="120px">
          <el-input v-model="form.bankCreditNum"></el-input>
          <div class="imgbox">
            <img :src="form.bankCreditUrl" class="img"/>
           </div>
           <div class="filenox" >
             <input type="file"  name="file" @change="getFile($event,'bankCreditUrl')" />
           </div>
        </el-form-item>
        </el-col>
    </el-row>
  </el-form>

</div>
</template>
<style scoped>
  .el-row {
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  h2{
    padding:0;
    text-align:left;
    border-bottom:1px solid #000;
    padding:0 0 6px 4px;
  }
  h5{
      text-align:left;
      padding:0 0 4px 14px;
      font-size:15px;
      color:#8c8c8c;
      border-bottom: solid 1px #eee;

  }
  .ed{
     border-radius:5px;
     padding:4px;
  }
  .img{
    width:140px;
    height:60px;
    border:1px solid #ddd;
    margin:8px 0 8px 0;
  }

</style>

<script>
import {getCompany,saveCompany,uploadFile} from '@/api'
import MomentPlugin from '@/libs/MomentPlugin'
export default {
  name: 'companyaudit',
  data () {
      return{
             tableData:[],
             //分页-数量
             pagesize: 10,
             //分页-页码
             currentPage: 1,
             //分页-总数
             totalCount: 100,
             form:{},
             formBase:{
               id:'',
               fullName:'',
               abbreviationName:'',
               dsc:''
             },
             formLink:{
               id:'',
               linkMan:'',
               mobile:'',
               email:'',
               address:''
             },
             formCredit:{
               id:'',
               idNumber:'',
               idCardFirstUrl:'',
               idCardSecondUrl:'',
               unifyCreditUrl:'',
               openingBankUrl:'',
               bankCreditUrl:'',
               unifyCreditNum:'',
               openingBankNum:'',
               bankCreditNum:''
             },
             multipleSelection: [],
             dialogFormVisible: false,
             dialogWidth:'500px',
             formLabelWidth: '80px'

      }
  },
  mounted(){
     var self=this;
     self.reload();
  },
  methods: {
    //分页数据加载
    loadData: function (criteria, pageNum, pagesize) {
      var self = this;
      var params ={};
      getCompany(params).then(result => {
        self.form = result;
      });
    },
    reload: function () {
      var self = this;
      self.loadData(null, self.currentPage, self.pagesize);
    },
    //分页大小变更
    handleSizeChange: function (val) {
      this.pagesize = val;
      this.loadData(this.criteria, this.currentPage, this.pagesize);
    },
    //分页变更
    handleCurrentChange: function (val) {
      this.currentPage = val;
      this.loadData(this.criteria, this.currentPage, this.pagesize);
    },

    //复选框
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //保存基础信息
    saveBaseInfo: function () {
      var self = this;
      self.formBase.id = self.form.id;
      self.formBase.fullName = self.form.fullName;
      self.formBase.abbreviationName = self.form.abbreviationName;
      self.formBase.dsc = self.form.dsc;
      var params = self.formBase;
      saveCompany(params).then(result => {
        if(result.code =="200"){
          alert("操作成功！");
          return;
        }else{
          alert("操作失败！");
        }
      });
    },
    //保存联系信息
    saveLinkInfo: function () {
      var self = this;
      self.formLink.id = self.form.id;
      self.formLink.linkMan = self.form.linkMan;
      self.formLink.mobile = self.form.mobile;
      self.formLink.email = self.form.email;
      self.formLink.address = self.form.address;
      var params = self.formLink;
      saveCompany(params).then(result => {
        if(result.code =="200"){
          alert("操作成功！");
          return;
        }else{
          alert("操作失败！");
        }
      });
    },
  //保存资质信息
    saveAuditInfo: function () {
      var self = this;
      self.formCredit.id = self.form.id;
      self.formCredit.idNumber = self.form.idNumber;
      self.formCredit.idCardFirstUrl = self.form.idCardFirstUrl;
      self.formCredit.idCardSecondUrl = self.form.idCardSecondUrl;
      self.formCredit.unifyCreditUrl = self.form.unifyCreditUrl;
      self.formCredit.openingBankUrl = self.form.openingBankUrl;
      self.formCredit.bankCreditUrl = self.form.bankCreditUrl;
      self.formCredit.unifyCreditNum = self.form.unifyCreditNum;
      self.formCredit.openingBankNum = self.form.openingBankNum;
      self.formCredit.bankCreditNum = self.form.bankCreditNum;
      var params = self.formCredit;
      saveCompany(params).then(result => {
        if(result.code =="200"){
          alert("操作成功！");
          return;
        }else{
          alert("操作失败！");
        }
      });
    },
    getFile(event,flag) {
      var file = event.target.files[0];
      if(file != undefined){
        this.uploadFile(file,flag);
      }
    },
    uploadFile(file,flag){
      event.preventDefault();
      var self=this;
      let formData = new FormData();
      formData.append("file",file);
      uploadFile({formData:formData,callback:function(result){
        console.log("uploadFile",result);
        if(result.code!="200"){
          alert(result.message);
          return;
        }
        alert(result.data.url);
        self.form[flag]=result.data.url;
      }});
    }

  }
}
</script>



