<template>
<div class="conwarp">
<!-- grid -->
<div class="grid">
    <div class="searchbar">
        <el-row>
            <el-col :span="4"><el-input  placeholder="请输入公司ID"   class="w18"  prefix-icon="el-icon-search" v-model="criteria.companyId" ></el-input> </el-col>
            <el-col :span="4">
              <el-select v-model="criteria.assetInfo" placeholder="接受资产类型" class="w18">
                <el-option v-for="item in criteria.assetInfoList" :key="item.code"  :label="item.itemName" :value="item.code"> </el-option>
              </el-select>
            </el-col>
            <el-col :span="4">
              <el-select v-model="criteria.foundCost" placeholder="资金成本" class="w18">
                <el-option v-for="item in criteria.foundCostList" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
              </el-select>
            </el-col>
            <el-col :span="4"><el-input  placeholder="募集失败率"   class="w18"  prefix-icon="el-icon-search" v-model="criteria.collectFailRate" ></el-input> </el-col>
            <el-col :span="4"><el-input  placeholder="公司名称"   class="w18"  prefix-icon="el-icon-search" v-model="criteria.companyName" ></el-input> </el-col>
        </el-row>
        <el-row>
            <el-col :span="4"><el-input  placeholder="资金方简称"   class="w18"  prefix-icon="el-icon-search" v-model="criteria.abbreviationName" ></el-input> </el-col>
            <el-col :span="4"><el-input  placeholder="业务区域(多个城市逗号分隔)"   class="w25"  prefix-icon="el-icon-search" v-model="criteria.businessArea" ></el-input> </el-col>
            <el-col :span="2"><el-button type="primary" icon="el-icon-search" @click="search">搜索</el-button></el-col>
          <el-col :span="2"><el-button type="primary" icon="el-icon-search" @click="handleExport">导出</el-button></el-col>
        </el-row>
    </div>
    <div class="gmain">
            <el-table :data="tableData"  ref="multipleTable"  tooltip-effect="dark"  @selection-change="handleSelectionChange" stripe style="width: 100%">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column  prop="companyId"   label="公司ID" ></el-table-column>
            <el-table-column  prop="abbreviationName"   label="资金方简称" ></el-table-column>
            <el-table-column  prop="assetInfo"   label="可接受资产类型" ></el-table-column>
            <el-table-column  prop="forecastAssetDemand"   label="预测资产需求量（万元）" ></el-table-column>
            <el-table-column  prop="foundCondition"   label="对资产的要求" ></el-table-column>
            <el-table-column  prop="foundYearRateRand"   label="资金年化利率范围" ></el-table-column>
            <el-table-column  prop="foundCost"   label="资金成本" ></el-table-column>
            <el-table-column  prop="monthTransactionRate"   label="月平均资金成交利率" ></el-table-column>
            <el-table-column  prop="collectFailRate"   label="募集失败率" ></el-table-column>
            <el-table-column label="操作"  >
                 <template slot-scope="scope">
                   <el-button size="small" type="primary"   @click="handleAddCooperation(scope.$index, scope.row)">加入合作名单</el-button>
                 </template>
            </el-table-column>
        </el-table>
        <div align="center" class="gpage">
                <el-pagination
                     background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20,30]"
                    :page-size="pagesize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="totalCount">
                </el-pagination>
        </div>
    </div>
</div>
</div>
</template>
<!--
<style>
  .el-row {
    margin-bottom: 20px;
  }
</style>-->
<script>
import {foundCompanyList,addMyFound,exportFoundCompany} from '@/api'
import MomentPlugin from '@/libs/MomentPlugin'
export default {
  name: 'companylist',
  data () {
      return{
             tableData:[],
             //分页-数量
             pagesize: 10,
             //分页-页码
             currentPage: 1,
             //分页-总数
             totalCount: 100,
             //搜索条件
             criteria: {
                 companyId:'',
                 assetInfo:'',
                 foundCost:'',
                 collectFailRate:'',
                 companyName:'',
                 abbreviationName:'',
                 businessArea:'',
                 assetInfoList:[
                   {code: '', itemName: '请选择接受资产类型'},
                    {code: '10', itemName: '消费分期',checked:false},
                     {code: '11', itemName: '融资租赁',checked:false},
                     {code: '12', itemName: '车抵贷',checked:false},
                     {code: '13', itemName: '房抵贷',checked:false},
                     {code: '14', itemName: '信用贷',checked:false},
                     {code: '15', itemName: '供应链金融',checked:false},
                     {code: '16', itemName: '其他',checked:false},
                 ],
                 subAssetInfoList:[
                      {code: '1001', itemName: '3C消费',checked:false},
                      {code: '1002', itemName: '个人日常消费',checked:false},
                      {code: '1003', itemName: '装修',checked:false},
                      {code: '1004', itemName: '旅游',checked:false},
                      {code: '1005', itemName: '教育',checked:false},
                      {code: '1006', itemName: '医疗',checked:false},
                      {code: '1007', itemName: '其他',checked:false},
                ],
                 foundCostList:[
                    {value: '', label: '请选择资金成本'},
                    {value: '0', label: '0%~5%'},
                    {value: '1', label: '5%~10%'},
                    {value: '2', label: '10%~15%'},
                    {value: '3', label: '15%~20%'},
                    {value: '4', label: '其他'},
                 ]
             },

             multipleSelection: [],
             dialogWidth:'500px',
             formLabelWidth: '80px'

      }
  },
  mounted(){
     var self=this;
     self.reload();
  },
  methods: {
    //分页数据加载
    loadData: function (criteria, pageNum, pagesize) {
      console.log("load");
      var self = this;
      var params = {page: pageNum, size: pagesize, search: ""};
      if (criteria != null) {
        params.search=JSON.stringify({
          companyId:criteria.companyId,
          assetInfo:criteria.assetInfo,
          foundCost:criteria.foundCost,
          collectFailRate:criteria.collectFailRate,
          fullName:criteria.companyName,
          abbreviationName:criteria.abbreviationName,
          businessArea:criteria.businessArea
          });

      }
      foundCompanyList(params).then(result => {
        var data = result.data;
        self.pagesize = data.pageSize;
        self.totalCount = data.total;
        self.tableData = data.list;

        var newAssetInfoList = self.criteria.assetInfoList.concat(self.criteria.subAssetInfoList);
        var temp;
        var tempArray;
        var tempAssetCode;
        var tempAsset;

        for(var i = 0; i < self.tableData.length; i++){
            temp = self.tableData[i];
            var resultString = "";
            if(temp.assetInfo != null && temp.assetInfo != ''){
                tempArray = temp.assetInfo.split(',');
                for(var j = 0; j < tempArray.length; j++){
                    tempAssetCode = tempArray[j];
                    for(var k = 0; k < newAssetInfoList.length; k++){
                      tempAsset = newAssetInfoList[k];
                      if(tempAsset.code == tempAssetCode){
                          if(resultString == ""){
                            resultString += tempAsset.itemName;
                          }
                          else{
                            resultString += "，" + tempAsset.itemName;
                          }
                      }
                    }
                }
            }
            temp.assetInfo = resultString;
        }
      });
    },
    handleAddCooperation:function(index, row) {
        console.log("d:",index, row);
        var self=this;

        addMyFound({companyId:row.companyId}).then(result => {
              console.log('del:',result);
             if(result.code!="200"){
                this.$message.error(result.message);
                return;
             }
             self.reload();
             this.$message({type: 'success',message: '添加成功!'});
          });
    },
    reload: function () {
      var self = this;
      self.loadData(null, self.currentPage, self.pagesize);
    },
    restForm: function () {
      var self = this;
      self.form = {id: '', name: '', img: '', useStatus: '0'};
      self.dialogFormVisible = false;
    },
    close: function () {
      var self = this;
      self.restForm();
    },
    //分页大小变更
    handleSizeChange: function (val) {
      this.pagesize = val;
      this.loadData(this.criteria, this.currentPage, this.pagesize);
    },
    //分页变更
    handleCurrentChange: function (val) {
      this.currentPage = val;
      this.loadData(this.criteria, this.currentPage, this.pagesize);
    },

    //复选框
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //查询
    search: function () {
      var self = this;
      console.log("search");
      this.loadData(self.criteria, this.currentPage, this.pagesize);
    },
    handleExport: function () {
      var self = this;
      this.export(self.criteria);
    },
    export:function(criteria){//导出表格
      var self = this;
      var params = {search: ""};
      if (criteria != null) {
        params.search=JSON.stringify({
          companyId:criteria.companyId,
          assetInfo:criteria.assetInfo,
          foundCost:criteria.foundCost,
          collectFailRate:criteria.collectFailRate,
          fullName:criteria.companyName,
          abbreviationName:criteria.abbreviationName,
          businessArea:criteria.businessArea,
          exportType:'1'
        });

      }
      var form = document.createElement('form');
      form.name = 'form'
      document.body.appendChild(form);
      for(var obj in params) {
        if(params.hasOwnProperty(obj)) {
          var input = document.createElement('input');
          input.tpye='hidden';
          input.name = obj;
          input.value = params[obj];
          form.appendChild(input)
        }
      }
      form.method = "post";//请求方式                                            ?access_token=
      form.action = exportFoundCompany();
      form.submit();
      document.body.removeChild(form);

    },
  }
}
</script>



