<template>
  <div class="conwarp">
    <h2>资产公司信息概况  &nbsp;  &nbsp; &nbsp; &nbsp; &nbsp;<span><el-button :span="2" type="primary" @click="saveAssetCompany">保存</el-button></span>

<!--
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span style="color: red">审核状态：</span>
      <span v-if="form.auditStatus==0" style="color: red">待资质认证</span>
      <span v-if="form.auditStatus==1" style="color: red">待审核资质</span>
      <span v-if="form.auditStatus==2" style="color: red">完成资质认证</span>
      <span v-if="form.auditStatus==3" style="color: red">资质认证失败</span>
-->

    </h2>
    <el-form ref="form" :model="form" label-width="200px">
      <h5 > 公司ID：<span >{{companyNo}}</span>&nbsp;  &nbsp; &nbsp;  &nbsp; &nbsp; &nbsp; &nbsp;公司名称:<span>{{companyFullName}}</span></h5>
      <input type="hidden" v-model="form.id"/>
      <el-row>
        <el-col :span="13">
          <el-form-item label="可以提供资产类型：">
            <el-checkbox v-for="check in assetTypeCheck" :label="check.itemName" :key="check.code" v-model="check.checked" @change="handleAssetTypeCheckChange(check.code)"></el-checkbox>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="6">
          <el-form-item label="资产规模">
            <el-input v-model="form.assetSize"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="6">
          <el-form-item label="月新增规模">
            <el-input v-model="form.monthSize"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="6">
          <el-form-item label="单笔资产期限">
            <el-input v-model="form.everyDeadline"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="6">
          <el-form-item label="单笔资产额度">
            <el-input v-model="form.everySum"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="4">
        <el-form-item label="逾期情况">
          <el-col :span="2" >
            M0： <el-input v-model="form.overdueM0"></el-input>
          </el-col>
          <el-col :span="2">
            M1： <el-input v-model="form.overdueM1"></el-input>
          </el-col>
          <el-col :span="2">
            M2：<el-input v-model="form.overdueM2"></el-input>
          </el-col>
          <el-col :span="2">
            M3：<el-input v-model="form.overdueM3"></el-input>
          </el-col>
        </el-form-item>
      </el-row>
      <el-row>
        <el-col :span="6">
          <el-form-item label="可接受资金成本">
            <el-input v-model="form.acceptFound"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="6">
          <el-form-item label="公司业务区域">
            <el-input v-model="form.businessArea"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="6">
          <el-form-item label="是否有履行约">
            <el-radio-group v-model="form.isPerformanceInsurance">
              <template>
                <el-radio :label="0">无</el-radio>
                <el-radio :label="1">有</el-radio>
              </template>
            </el-radio-group>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="6">
          <el-form-item label="是否有担保人">
            <el-radio-group v-model="form.isGuarantee">
            <template>
              <el-radio :label="0">无</el-radio>
              <el-radio :label="1">有</el-radio>
            </template>
            </el-radio-group>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>

  </div>
</template>
<style scoped>
  .el-row {
    margin-bottom: 10px;
  &:last-child {
     margin-bottom: 0;
   }
  }
  h2{
    padding:0;
    text-align:left;
    border-bottom:1px solid #000;
    padding:0 0 6px 4px;
  }
  h5{
    text-align:left;
    padding:0 0 4px 14px;
    font-size:15px;
    color:#8c8c8c;
    border-bottom: solid 1px #eee;

  }
  .{
    width:140px;
    height:60px;
    border:1px solid #ddd;
    margin:8px 0 8px 0;
  }
</style>

<script>
  import {getAssetCompany,saveAssetCompany} from '@/api'
  import MomentPlugin from '@/libs/MomentPlugin'
  export default {
    name: 'companylist',
    data () {
      return{
        tableData:[],
        //分页-数量
        pagesize: 10,
        //分页-页码
        currentPage: 1,
        //分页-总数
        totalCount: 100,
        companyFullName:'',
        companyNo:'',
        assetTypeCheck:[
          {code: '11', itemName: '融资租赁',checked:false},
          {code: '12', itemName: '车抵贷',checked:false},
          {code: '13', itemName: '房抵贷',checked:false},
          {code: '14', itemName: '信用贷',checked:false},
          {code: '15', itemName: '供应链金融',checked:false},
          {code: '16', itemName: '其他',checked:false},
          {code: '10', itemName: '消费分期',checked:false},
        ],
        subAssetCheck:[
        {code: '1001', itemName: '3C消费',checked:false},
        {code: '1002', itemName: '个人日常消费',checked:false},
        {code: '1003', itemName: '装修',checked:false},
        {code: '1004', itemName: '旅游',checked:false},
        {code: '1005', itemName: '教育',checked:false},
        {code: '1006', itemName: '医疗',checked:false},
        {code: '1007', itemName: '其他',checked:false},
      ],
        form:{
          id:'',
          assetInfo:'',
          assetSize:'',
          monthSize:'',
          everyDeadline:'',
          everySum:'',
          overdueM0:'',
          overdueM1:'',
          overdueM2:'',
          overdueM3:'',
          acceptFound:'',
          businessArea:'',
          isGuarantee:'',
          isPerformanceInsurance:''
        },
        multipleSelection: [],
        dialogFormVisible: false,
        dialogWidth:'500px',
        formLabelWidth: '80px'

      }
    },
    mounted(){
      var self=this;
      self.loadData();
    },
    methods: {
      loadData: function () {
        var self = this;
        var params={};
        getAssetCompany(params).then(result => {
          console.log("result",result)
          if(result == null){
            return false;
          }
         self.companyFullName=result.fullName;
         self.companyNo=result.companyNo;
         self.form = result;


          if(self.form.assetInfo.length > 0){

            if(self.form.assetInfo.match("10") != null){
              self.assetTypeCheck = self.assetTypeCheck.concat(self.subAssetCheck);
            }
            var tempAreray = self.assetTypeCheck;
            var arr = self.form.assetInfo.split(',');

            for(var i = 0; i < tempAreray.length; i++){
              var item1 = tempAreray[i];
              for(var j = 0; j < arr.length; j++){
                var item2 = arr[j];
                if(item1.code == item2){
                  item1.checked = true;
                }
              }
            }
          }

        });
      },


      handleAssetTypeCheckChange: function(code){
        var tempAreray = this.subAssetCheck;
        for(var i = 0; i < this.assetTypeCheck.length; i++){
          var item = this.assetTypeCheck[i];
          if(item.code == code){
            if(code == '10'){
              if(item.checked == true){
                this.assetTypeCheck = this.assetTypeCheck.concat(tempAreray);
              }
              else{
                this.assetTypeCheck.splice(7,7);
                for(var j = 0; j < this.subAssetCheck.length; j++){
                  var item1 = this.subAssetCheck[j];
                  item1.checked = false;
                }
              }
            }
          break;
        }
        }
      },
      restForm: function () {
        var self = this;
        self.form = {id: '', name: '', img: '', useStatus: '0'};
        self.dialogFormVisible = false;
      },
      close: function () {
        var self = this;
        self.restForm();
      },
      //分页大小变更
      handleSizeChange: function (val) {
        this.pagesize = val;
        this.loadData(this.criteria, this.currentPage, this.pagesize);
      },
      //分页变更
      handleCurrentChange: function (val) {
        this.currentPage = val;
        this.loadData(this.criteria, this.currentPage, this.pagesize);
      },

      //复选框
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      saveAssetCompany: function () {
        var self = this;
        var assetInfo = "";
        for(var i = 0; i < self.assetTypeCheck.length; i++){
          var item1 = self.assetTypeCheck[i];
          if(item1.checked == true){
            if(assetInfo.length == 0){
              assetInfo += item1.code;
            }else{
              assetInfo += "," + item1.code;
            }
          }
        }
        self.form.assetInfo = assetInfo;
        var params = self.form;
        saveAssetCompany(params).then(result => {
          if(result.code =="200"){
            alert(result.message);
            return;
          }else{
            alert("操作失败！");
          }
         });
      },
    }
  }
</script>



