<template>
<div class="conwarp">
<!-- grid -->
<div class="grid">
    <div class="searchbar">
        <el-row :gutter="20" >
            <el-col :span="4"><el-input  placeholder="请输入企业编号"   class="w18"  prefix-icon="el-icon-search" v-model="criteria.id" ></el-input> </el-col>
            <el-col :span="4">
              <el-select v-model="criteria.companyType" placeholder="公司类别">
                <el-option v-for="item in criteria.cType" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
              </el-select>
            </el-col>
            <el-col :span="4">
              <el-select v-model="criteria.auditStatus" placeholder="账号状态">
                <el-option v-for="item in criteria.aStatus" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
              </el-select>
            </el-col>


            <el-col :span="2"><el-button type="primary" icon="el-icon-search" @click="search">搜索</el-button></el-col>
            <el-col :span="2"><el-button type="primary" @click="search">审核资质</el-button></el-col>
            <el-col :span="2"><el-button type="primary" @click="handleExport">导出</el-button></el-col>
            <span class="fileinput-button el-button el-button--primary el-button--small">
              <span >导入公司信息认证</span>
              <input type="file"  name="file" @change="getFile($event,'1')" />
            </span>
            <span class="fileinput-button el-button el-button--primary el-button--small">
              <span >导入资产公司信息</span>
              <input type="file"  name="file" @change="getFile($event,'2')" />
            </span>
            <span class="fileinput-button el-button el-button--primary el-button--small">
              <span >导入资金公司信息</span>
              <input type="file"  name="file" @change="getFile($event,'3')" />
            </span>

        </el-row>
        <el-row>
            <el-col :span="4"><el-input  placeholder="公司名称"   class="w18"  prefix-icon="el-icon-search" v-model="criteria.fullName" ></el-input> </el-col>
            <el-col :span="4"><el-input  placeholder="公司简称"   class="w18"  prefix-icon="el-icon-search" v-model="criteria.abbreviationName" ></el-input> </el-col>
             <el-col :span="4">
              <el-date-picker  v-model="criteria.startDate" type="date" format="yyyy-MM-dd" value-format="yyyy-MM-dd HH:mm:ss"  placeholder="开始日期"> </el-date-picker>
            </el-col>
            <el-col :span="4">
              <el-date-picker  v-model="criteria.endDate" type="date" format="yyyy-MM-dd" value-format="yyyy-MM-dd HH:mm:ss"  placeholder="结束日期"> </el-date-picker>
            </el-col>
        </el-row>
    </div>
    <div class="gmain">
            <el-table :data="tableData"  ref="multipleTable"  tooltip-effect="dark"  @selection-change="handleSelectionChange" stripe style="width: 100%">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column  prop="id"   label="公司ID" ></el-table-column>
            <el-table-column  prop="abbreviationName"   label="简称" ></el-table-column>
            <el-table-column  prop="fullName"   label="全称" ></el-table-column>
            <el-table-column  prop="companyType"  label="账号类型" >
              <template scope="scope">
                <span v-if="scope.row.companyType==0">资产方</span>
                <span v-if="scope.row.companyType==1">资金方</span>
              </template>
            </el-table-column>
            <el-table-column  prop="auditStatus"  label="账号状态" >
              <template scope="scope">
                <span v-if="scope.row.auditStatus==0">待资质认证</span>
                <span v-if="scope.row.auditStatus==1">待审核资质</span>
                <span v-if="scope.row.auditStatus==2">完成资质认证</span>
                <span v-if="scope.row.auditStatus==3">审核不通过</span>
              </template>
            </el-table-column>
            <el-table-column  prop="createTime"   label="创建时间" ></el-table-column>
            <el-table-column label="操作"  >
                 <template slot-scope="scope">
                   <el-button size="small" type="primary"   @click="handleDetail(scope.$index, scope.row)">查看</el-button>
                   <el-button size="small" type="danger" v-if="scope.row.auditStatus==1"   @click="handleAudit(scope.$index, scope.row)">审核</el-button>
                 </template>
            </el-table-column>
        </el-table>
        <div align="center" class="gpage">
                <el-pagination
                     background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20,30]"
                    :page-size="pagesize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="totalCount">
                </el-pagination>
        </div>
    </div>
</div>


<el-dialog title="查看合作伙伴" :visible.sync="dialoCompanyFormVisible" :before-close="close" :width="dialogWidth" class="demo-ruleForm">
  <el-tabs type="border-card" v-model="cardItemName">
    <el-tab-pane name="baseInfo" label="基础信息">
        <el-row>
          <el-col :span="5"><div align="left">公司ID：</div></el-col>
          <el-col :span="18"><div align="left">{{baseCompanyInfo.id}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">公司名称：</div></el-col>
          <el-col :span="18"><div align="left">{{baseCompanyInfo.fullName}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">公司简称：</div></el-col>
          <el-col :span="18"><div align="left">{{baseCompanyInfo.abbreviationName}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">公司介绍：</div></el-col>
          <el-col :span="18"><div align="left">{{baseCompanyInfo.dsc}}</div></el-col>
        </el-row>
  </el-tab-pane>
  <el-tab-pane label="联系人信息">
        <el-row>
          <el-col :span="5"><div align="left">联系人：</div></el-col>
          <el-col :span="18"><div align="left">{{baseCompanyInfo.linkMan}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">电话：</div></el-col>
          <el-col :span="18"><div align="left">{{baseCompanyInfo.mobile}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">邮箱：</div></el-col>
          <el-col :span="18"><div align="left">{{baseCompanyInfo.email}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">通信地址：</div></el-col>
          <el-col :span="18"><div align="left">{{baseCompanyInfo.address}}</div></el-col>
        </el-row>
  </el-tab-pane>
  <el-tab-pane label="资质信息">
        <el-row>
          <el-col :span="5"><div align="left">法人身份证：</div></el-col>
          <el-col :span="18"><div align="left">{{baseCompanyInfo.idNumber}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"></el-col>
          <el-col :span="18">
              <img :src="baseCompanyInfo.idCardFirstUrl" width="20%" alt="front" />
              <img :src="baseCompanyInfo.idCardSecondUrl" width="20%" alt="front" />
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">社会信用代码证：</div></el-col>
          <el-col :span="18"><div align="left">{{baseCompanyInfo.unifyCreditNum}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"></el-col>
          <el-col :span="18">
              <img :src="baseCompanyInfo.unifyCreditUrl" width="20%" alt="front" />
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">银行开户许可证：</div></el-col>
          <el-col :span="18"><div align="left">{{baseCompanyInfo.openingBankNum}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"></el-col>
          <el-col :span="18">
              <img :src="baseCompanyInfo.openingBankUrl" width="20%" alt="front" />
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">银行信用证：</div></el-col>
          <el-col :span="18"><div align="left">{{baseCompanyInfo.bankCreditNum}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"></el-col>
          <el-col :span="18">
              <img :src="baseCompanyInfo.bankCreditUrl" width="20%" alt="front" />
          </el-col>
        </el-row>
    </el-tab-pane>
    <el-tab-pane label="资金信息" v-if="baseCompanyInfo.companyType==1">
        <el-row>
          <el-col :span="5"><div align="left">接受资产类型:</div></el-col>
          <el-col :span="18"><div align="left">{{detailCompanyInfo.assetInfo}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">预测资产需求量：</div></el-col>
          <el-col :span="18"><div align="left">{{detailCompanyInfo.forecastAssetDemand}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">对资产的要求：</div></el-col>
          <el-col :span="18"><div align="left">{{detailCompanyInfo.foundCondition}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">资金年化利率范围：</div></el-col>
          <el-col :span="18"><div align="left">{{detailCompanyInfo.foundYearRateRand}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">资金成本：</div></el-col>
          <el-col :span="18"><div align="left">{{detailCompanyInfo.foundCost}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">月平均资金成交利率：</div></el-col>
          <el-col :span="18"><div align="left">{{detailCompanyInfo.monthTransactionRate}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">募集失败率：</div></el-col>
          <el-col :span="18"><div align="left">{{detailCompanyInfo.collectFailRate}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">公司业务区域：</div></el-col>
          <el-col :span="18"><div align="left">{{detailCompanyInfo.businessArea}}</div></el-col>
        </el-row>
    </el-tab-pane>
    <el-tab-pane label="资产信息" v-if="baseCompanyInfo.companyType==0">
        <el-row>
          <el-col :span="5"><div align="left">可以提供资产类型:</div></el-col>
          <el-col :span="18"><div align="left">{{detailCompanyInfo.assetInfo}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">资产规模：</div></el-col>
          <el-col :span="18"><div align="left">{{detailCompanyInfo.assetSize}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">月新增规模：</div></el-col>
          <el-col :span="18"><div align="left">{{detailCompanyInfo.monthSize}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">单笔资产期限：</div></el-col>
          <el-col :span="18"><div align="left">{{detailCompanyInfo.everyDeadline}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">单笔资产额度：</div></el-col>
          <el-col :span="18"><div align="left">{{detailCompanyInfo.everySum}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">逾期情况：</div></el-col>
          <el-col :span="1"><div align="left">M0:</div></el-col>
          <el-col :span="3"><div align="left">{{detailCompanyInfo.overdueM0}}</div></el-col>
          <el-col :span="1"><div align="left">M1:</div></el-col>
          <el-col :span="3"><div align="left">{{detailCompanyInfo.overdueM1}}</div></el-col>
          <el-col :span="1"><div align="left">M2:</div></el-col>
          <el-col :span="3"><div align="left">{{detailCompanyInfo.overdueM2}}</div></el-col>
          <el-col :span="1"><div align="left">M3:</div></el-col>
          <el-col :span="3"><div align="left">{{detailCompanyInfo.overdueM3}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">可接受资金成本：</div></el-col>
          <el-col :span="18"><div align="left">{{detailCompanyInfo.acceptFound}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">公司业务区域：</div></el-col>
          <el-col :span="18"><div align="left">{{detailCompanyInfo.businessArea}}</div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">是否有履行约：</div></el-col>
          <el-col :span="18">
            <div align="left" v-if="detailCompanyInfo.isPerformanceInsurance==0">否</div>
            <div align="left" v-if="detailCompanyInfo.isPerformanceInsurance==1">是</div>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="5"><div align="left">是否有担保人：</div></el-col>
          <el-col :span="18">
            <div align="left" v-if="detailCompanyInfo.isGuarantee==0">否</div>
            <div align="left" v-if="detailCompanyInfo.isGuarantee==1">是</div>
          </el-col>
        </el-row>
    </el-tab-pane>
  </el-tabs>
</el-dialog>

<el-dialog title="审核" :visible.sync="dialogAuditFormVisible" :before-close="close1" :width="dialogAuditWidth" class="demo-ruleForm">
    <el-form :model="audit" :rules="rules" ref="audit" label-width="80px">
        <el-form-item label="审核结果" prop="status">
            <el-select v-model="audit.status" placeholder="请选择">
                <el-option label="审核通过" value="2"></el-option>
                <el-option label="审核不通过" value="3"></el-option>
            </el-select>
        </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
        <el-button @click="close()">取 消</el-button>
        <el-button type="primary" @click="toAudit()">确 定</el-button>
    </div>
</el-dialog>

</div>
</template>
<style>
   .el-row {
    margin-bottom: 10px;
   }
   .fileinput-button {
     position: relative;
     display: inline-block;
     overflow: hidden;
     font-size: 12px;
     border-radius: 3px
   }

   .fileinput-button input{
     position:absolute;
     right: 0px;
     top:0px;
     opacity: 0;
     -ms-filter: 'alpha(opacity=0)';
     font-size: 200px;
   }

</style>

<script>
import {getCompanyDetail,getCompanyList,auditCompany,exportCompany,uploadCompanyExcel} from '@/api'
import MomentPlugin from '@/libs/MomentPlugin'
export default {
  name: 'companylist',
  data () {
      return{
             tableData:[],
             //分页-数量
             pagesize: 10,
             //分页-页码
             currentPage: 1,
             //分页-总数
             totalCount: 100,
             //搜索条件
             criteria: {
                 id:'',
                 companyType:'',
                 cType:[
                   {value: '', label: '全部'},
                   {value: '0', label: '资产方'},
                   {value: '1', label: '资金方'}
                 ],
                 auditStatus:'',
                 aStatus:[
                   {value: '', label: '全部'},
                   {value: '0', label: '待资质认证'},
                   {value: '1', label: '待审核资质'},
                   {value: '2', label: '完成资质认证'}
                 ],
                 fullName:'',
                 abbreviationName:'',
                 startDate:'',
                 endDate:''
             },
             audit:{
                status:'',
                id:'',
             },
             cardItemName:'baseInfo',
             baseCompanyInfo:{},
             detailCompanyInfo:{},
             foundCheck:[
                     {code: '11', itemName: '融资租赁',checked:false},
                     {code: '12', itemName: '车抵贷',checked:false},
                     {code: '13', itemName: '房抵贷',checked:false},
                     {code: '14', itemName: '信用贷',checked:false},
                     {code: '15', itemName: '供应链金融',checked:false},
                     {code: '16', itemName: '其他',checked:false},
                     {code: '10', itemName: '消费分期',checked:false},
                      {code: '1001', itemName: '3C消费',checked:false},
                      {code: '1002', itemName: '个人日常消费',checked:false},
                      {code: '1003', itemName: '装修',checked:false},
                      {code: '1004', itemName: '旅游',checked:false},
                      {code: '1005', itemName: '教育',checked:false},
                      {code: '1006', itemName: '医疗',checked:false},
                      {code: '1007', itemName: '其他',checked:false},
            ],
             multipleSelection: [],
             dialoCompanyFormVisible: false,
             dialogAuditFormVisible: false,
             dialogWidth:'700px',
             dialogAuditWidth:'400px',
             formLabelWidth: '80px'

      }
  },
  mounted(){
     var self=this;
     self.reload();
  },
  methods: {
    //分页数据加载
    loadData: function (criteria, pageNum, pagesize) {
      console.log("load");
      var self = this;
      var params = {page: pageNum, size: pagesize, search: ""};
      if (criteria != null) {
        params.search=JSON.stringify({
          id:criteria.id,
          companyType:criteria.companyType,
          auditStatus:criteria.auditStatus,
          fullName:criteria.fullName,
          abbreviationName:criteria.abbreviationName,
          startTime:criteria.startTime,
          endtime:criteria.endTime});

      }
      getCompanyList(params).then(result => {
        var data = result.data;
        self.pagesize = data.pageSize;
        self.totalCount = data.total;
        self.tableData = data.list;
      });
    },
    reload: function () {
      var self = this;
      self.loadData(null, self.currentPage, self.pagesize);
    },
    restForm: function () {
      var self = this;
      self.form = {id: '', name: '', img: '', useStatus: '0'};
      self.dialoCompanyFormVisible = false;
    },
    close: function () {
      var self = this;
      self.restForm();
    },
    close1: function () {
      var self = this;
      self.dialoCompanyFormVisible = false;
    },
    //分页大小变更
    handleSizeChange: function (val) {
      this.pagesize = val;
      this.loadData(this.criteria, this.currentPage, this.pagesize);
    },
    //分页变更
    handleCurrentChange: function (val) {
      this.currentPage = val;
      this.loadData(this.criteria, this.currentPage, this.pagesize);
    },

    //复选框
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //查询
    search: function () {
      var self = this;
      console.log("search");
      this.loadData(self.criteria, this.currentPage, this.pagesize);
    },
    handleDetail:function(index, row) {
        var self=this;
        self.baseCompanyInfo = row;
        self.cardItemName = "baseInfo";

        var params = {id:row.id,companyType:row.companyType};
        getCompanyDetail(params).then(result => {
            var data = result.data;
            self.detailCompanyInfo = data;
            if(row.companyType == 0){
               self.showDefault(self.detailCompanyInfo.assetInfo);
            }
            else{
               self.showDefault(self.detailCompanyInfo.assetInfo);
            }

            self.dialoCompanyFormVisible = true;
        });

    },
    handleAudit:function(index, row) {
        var self=this;
        self.audit.id = row.id;
        self.dialogAuditFormVisible = true;
    },
    showDefault:function(assetInfo){
      var self = this;

      if(assetInfo != null && assetInfo.length > 0){
        var resultString = '';
        var tempAreray = self.foundCheck;
        var arr = assetInfo.split(',');

          for(var i = 0; i < tempAreray.length; i++){
            var item1 = tempAreray[i];
            for(var j = 0; j < arr.length; j++){
              var item2 = arr[j];
              if(item1.code == item2){
                if(resultString.length == 0){
                  resultString += item1.itemName;
                }
                else{
                  resultString += '，' + item1.itemName;
                }
              }
            }
        }

        self.detailCompanyInfo.assetInfo = resultString;
      }
    },
     toAudit:function(){
        var self=this;

        auditCompany({id:self.audit.id,status:self.audit.status}).then(result => {
            if(result.code!="200"){
                this.$message.error(result.message);
                return;
            }
            this.$message({type: 'success',message: '审核成功!'});
            self.dialogAuditFormVisible=false;
            self.reload();
        });

     },
    export:function(criteria){//导出表格
      var self = this;
      var params = {search: ""};
      if (criteria != null) {
        params.search=JSON.stringify({
          id:criteria.id,
          companyType:criteria.companyType,
          auditStatus:criteria.auditStatus,
          fullName:criteria.fullName,
          abbreviationName:criteria.abbreviationName,
          startTime:criteria.startTime,
          endtime:criteria.endTime});

      }
      var form = document.createElement('form');
      form.name = 'form'
      document.body.appendChild(form);
      for(var obj in params) {
        if(params.hasOwnProperty(obj)) {
          var input = document.createElement('input');
          input.tpye='hidden';
          input.name = obj;
          input.value = params[obj];
          form.appendChild(input)
        }
      }
      form.method = "post";//请求方式                                            ?access_token=
      form.action = exportCompany();
      form.submit();
      document.body.removeChild(form);

    },
    handleExport: function () {
      var self = this;
      this.export(self.criteria);
    },
    getFile: function(event,type) {
      var file = event.target.files[0];
      if(file != undefined){
        this.uploadFile(file,type);
      }
    },
    uploadFile(file,type){
      event.preventDefault();
      var self=this;
      let formData = new FormData();
      formData.append("file",file);
      formData.append("type",type);
      uploadCompanyExcel({formData:formData,callback:function(result){
        alert(result.message);
        self.reload();
      }});
    }
  }

}
</script>



