<template>
  <div>
   
   
    <div class="container" style="width: 400px; margin: 0 auto;">
     <img src="../assets/logo.png" style="width:180px;margin: 100px 0 0 80px; " >
      <h1  style="color:#f1c232; text-align: center; margin:0 0 10px 80px;">{{ msg }}</h1>
          <el-form ref="loginForm" :model="loginForm" :rules="rules"  label-width="80px">
          <el-form-item label="账号" prop="username">
              <el-input v-model="loginForm.username"  placeholder="请输入账号"></el-input>
          </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input type="password" v-model="loginForm.password" placeholder="请输入密码" @keyup.enter.native="onSubmit('loginForm')" ></el-input>
          </el-form-item>
            <el-form-item>
              <el-button  @click="onSubmit('loginForm')">登录</el-button>
          </el-form-item>
      </el-form>
  </div>
</div>
</template>
<script>
import {login,getUserInfo} from '../api'
export default {
  name: 'Login',
  meta: {
      title: 'Super-P2P',
  },
  data () {
    return {
      msg: 'Super-P2P',
      loginForm:{
        username:'',
        password:''
      },
      rules:{
        username: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }]
      }
    }
  },
  methods:{
    /**
     * 登录
     */
    onSubmit(formName){
       const self = this;      
       //表单验证
       self.$refs[formName].validate((valid) => {
            if(valid){
              //请求登录
              var params={username:self.loginForm.username,password:self.loginForm.password};
              login(params).then(result => {
                 if(result.code!=200){
                    self.err(result);
                    return;
                 }
                 result=result.data;
                  console.log(result);
                  if(result["access_token"]){
                      //记录tokenInfo
                      sessionStorage.setItem("access_token",result.access_token);
                      sessionStorage.setItem("appSecret",result.appSecret);
                      sessionStorage.setItem("userType",result.userType);
                      sessionStorage.setItem("realName",result.realName);
                      sessionStorage.setItem("sex",result.sex);
                      self.GLOBAL.tokenSession=result;
                      self.$store.state.userinfo.username=self.loginForm.username;
                      self.$store.state.userinfo.tokeninfo=result;
                      self.$router.push('/home');
                      //self.inside(self.$store.state.userinfo);    
                      //sessionStorage.userType     
                      //sessionStorage.appSecret                                  
                  }else{
                    
                  }
                 
              });
            }
       });
    },
    err(msg){
        this.$message.error(msg);
    },
    /**
     * 加载菜单进入Home
     */
    inside(userinfo){
      const self = this;    
      var qr="appcode="+userinfo.appcode;
      getUserInfo(qr).then(result => {
           console.log("get-user-info:",result);
           self.$store.state.userinfo.menu=result.data.menus;
           console.log('session:', self.$store);  
           self.$router.push('/home');
      });
    }
  }
}
</script>


