<template>
<div class="conwarp">
<!-- grid -->
<div class="grid">
    <div class="toolbar">
        <el-button type="primary"  icon="el-icon-plus" @click="dialogFormVisible=true" >添加</el-button>
    </div>
    <div class="searchbar">
        <el-row>
            <el-col :span="3"><el-input  placeholder="请输入名称"   class="w18"  prefix-icon="el-icon-search" v-model="criteria.righName" ></el-input> </el-col>
     
            <el-col :span="4">
                 <el-select v-model="criteria.rightType" placeholder="权限类型">
                    <el-option v-for="item in criteria.rightTypes" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
                 </el-select>
            </el-col>
            <el-col :span="4">  
                <el-select v-model="criteria.userStatus" placeholder="权限状态">
                    <el-option v-for="item in criteria.status" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
                 </el-select>
            </el-col>
            <el-col :span="4">
                <el-select v-model="criteria.visibleType" placeholder="请选择可见类型">
                    <el-option label="可见类型"   value=""></el-option>
                    <el-option label="用户可见"   value="0"></el-option>
                    <el-option label="管理员可见" value="1"></el-option>
                </el-select>
            </el-col>
          
            <el-col :span="4"><el-button type="primary" icon="el-icon-search" @click="search">搜索</el-button></el-col>
        </el-row>
        
        

    </div>
    <div class="gmain">
            <el-table :data="tableData"  ref="multipleTable"  tooltip-effect="dark"  @selection-change="handleSelectionChange" stripe style="width: 100%">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column  prop="id"   label="编号" ></el-table-column>
            <el-table-column  prop="parentId"   label="父编号" ></el-table-column>
            <el-table-column  prop="rightOrder"   label="权限排序" ></el-table-column>
            <el-table-column  prop="rightName"   label="权限名称" ></el-table-column>
            <el-table-column  prop="rightUrl"   label="权限URL" ></el-table-column>
            <el-table-column  prop="rightType"   label="权限类型"  >
                 <template scope="scope">
                    <span v-if="scope.row.rightType==0" >一级菜单</span>
                    <span v-if="scope.row.rightType==1" >二级菜单</span>
                    <span v-if="scope.row.rightType==2" >按钮</span>
                </template>
            </el-table-column>
            <el-table-column  prop="userStatus"   label="权限状态" >
                <template scope="scope">
                    <span v-if="scope.row.userStatus==0" style="color:red;" >使用</span>
                    <span v-if="scope.row.userStatus==1" style="color:green;" >停止</span>
                </template>
            </el-table-column>
             <el-table-column  prop="visibleType"   label="可见类型" >
                <template scope="scope">
                    <span v-if="scope.row.visibleType==0" style="color:green;" >用户可见</span>
                    <span v-if="scope.row.visibleType==1" style="color:red;" >管理员可见</span>
                </template>
            </el-table-column>
            <el-table-column label="操作"  > 
                 <template slot-scope="scope">
                    <el-button size="small" type="primary"  @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button size="small" type="danger"   @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                 </template>
            </el-table-column>
        </el-table>
        <div align="center" class="gpage">
                <el-pagination
                     background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20,30]"
                    :page-size="pagesize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="totalCount">
                </el-pagination>
        </div>
    </div>
</div>
<!-- form -->
<el-dialog title="权限信息" :visible.sync="dialogFormVisible" :before-close="close" :width="dialogWidth">
    <el-form :model="form" :rules="rules" ref="form">
        <el-form-item label="父编号" :label-width="formLabelWidth" prop="parentId">
            <input type="hidden" v-model="form.id"/>
            <el-input v-model="form.parentId" auto-complete="off"></el-input>
        </el-form-item>

        <el-form-item label="权限名称" :label-width="formLabelWidth" prop="rightName">
            <el-input v-model="form.rightName" auto-complete="off" size="small"></el-input>
        </el-form-item>
        <el-form-item label="权限地址" :label-width="formLabelWidth" prop="rightUrl">
            <el-input v-model="form.rightUrl" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="排序" :label-width="formLabelWidth" prop="rightOrder">
            <el-input v-model="form.rightOrder" auto-complete="off" size="small"></el-input>
        </el-form-item>
        <el-form-item label="权限类型" :label-width="formLabelWidth">
            <el-select v-model="form.rightType" placeholder="请选择应用类型">
                <el-option label="一级菜单" value="0"></el-option>
                <el-option label="二级菜单" value="1"></el-option>
                <el-option label="按钮" value="2"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="可见类型" :label-width="formLabelWidth">
            <el-select v-model="form.visibleType" placeholder="请选择可见类型">
                <el-option label="用户可见" value="0"></el-option>
                <el-option label="管理员可见" value="1"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="权限状态" :label-width="formLabelWidth">
            <el-select v-model="form.userStatus" placeholder="请选择权限状态">
                <el-option label="使用" value="0"></el-option>
                <el-option label="停止" value="1"></el-option>
            </el-select>
        </el-form-item>
         <el-form-item label="权限描述" :label-width="formLabelWidth" prop="description">
            <el-input v-model="form.description" auto-complete="off" size="small"></el-input>
        </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible=false">取 消</el-button>
        <el-button type="primary" @click="formSave('form')">确 定</el-button>
    </div>

</el-dialog>
</div>
</template>

<script>
import {addRight,delRight,updateRight,getRightList} from '@/api'

export default {
  name: 'rightManage',
  data () {
      return{
             tableData:[],
             //分页-数量
             pagesize: 10,
             //分页-页码
             currentPage: 1,
             //分页-总数
             totalCount: 100,
             //搜索条件
             criteria: {
                 rightName:'',
                 startDate:'',
                 endDate:'',
                 rightType:'',
                 rightTypes:[
                     {value: '', label: '所有类型'},
                     {value: '0', label: '一级菜单'},
                     {value: '1', label: '二级菜单'},
                     {value: '2', label: '按钮'}
                 ],
                 userStatus:'',
                 status:[
                     {value: '', label: '所有状态'},
                     {value: '0', label: '使用'},
                     {value: '1', label: '停止'}
                 ],
                 visibleType:'0'
             },
             multipleSelection: [],
             form:{
                 id:'',
                 parentId:'',
                 rightName:'',
                 rightUrl:'',
                 rightOrder:'',
                 rightType:'0',
                 userStatus:'0',
                 description:'',
                 visibleType:'0'
             },
             rules:{
                rightName:[
                     { required: true, message: '请输入名称', trigger: 'blur' }
                    ]
             },
             dialogFormVisible: false,
             dialogWidth:'400px',
             formLabelWidth: '80px'
             
      }
  },
  mounted(){
     var self=this;
     self.reload();
  },
  methods:{
     //分页数据加载
     loadData: function(criteria, pageNum, pagesize){ 
          console.log("load");
        var self=this;
        var params={page:pageNum,size:pagesize,search:""};
        if(criteria!=null){
            params.search=JSON.stringify({rightName:criteria.rightName,rightType:criteria.rightType,userStatus:criteria.userStatus,visibleType:criteria.visibleType,startDate:criteria.startDate,endDate:criteria.endDate});    
        }
        getRightList(params).then(result => {
                var data=result.data;
                self.pagesize=data.pageSize;
                self.totalCount=data.total;
                self.tableData=data.list;
        });
     },
     reload:function(){
         var self=this;
          self.loadData(null,self.currentPage,self.pagesize);
     },
     restForm:function(){
        var self=this;
        self.form={
                 id:'',
                 parentId:'',
                 rightName:'',
                 rightUrl:'',
                 rightOrder:'',
                 rightType:'0',
                 userStatus:'0',
                 description:'',
                 visibleType:'0'
             };
        self.dialogFormVisible=false;
     },
     close:function(){
         var self=this;
         self.restForm();
     },
     //分页大小变更
     handleSizeChange: function(val) {
        this.pagesize = val;
        this.loadData(this.criteria, this.currentPage, this.pagesize);
     },
     //分页变更
     handleCurrentChange: function(val) {
        this.currentPage = val;
        this.loadData(this.criteria, this.currentPage, this.pagesize);
     },
     //复选框
     handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      //查询
      search:function(){
          var self=this;
          console.log("search");
          this.loadData(self.criteria, this.currentPage, this.pagesize);
      },
    //编辑
     handleEdit: function(index, row) {
        console.log("e:",index, row);
        var self=this;
        var detail=self.form;
        detail.id=row.id;
         detail.parentId=row.parentId;
        detail.rightName=row.rightName;
        detail.rightUrl=row.rightUrl;
        detail.rightOrder=row.rightOrder;
        detail.rightType=row.rightType.toString();
        detail.userStatus=row.userStatus.toString();
        detail.visibleType=row.visibleType.toString();
        detail.description=row.description;
        
        self.dialogFormVisible=true;
     },
     //删除
     handleDelete: function(index, row) {
          console.log("d:",index, row);
          var self=this;
          if(!confirm("你确定要删除吗？"))return;
          delRight({id:row.id}).then(result => {  
              console.log('del:',result);
             if(result.code!="200"){
                alert(result.message);
                return;
             }        
             self.reload();  
          });
     },
     //保存表单
     formSave(formName){
        var self=this;
        self.$refs[formName].validate((valid) => {
            console.log("valid:",valid);
            if(!valid)return;
          
            if(self.form.id==""){
                //add
                addRight(self.form).then(result => {
                    console.log("add:",result);
                    if(result.code!="200"){
                        alert(result.message);
                        return;
                    }                  
                    self.reload();
                    self.restForm();
                   
                });
            }else{
                //update
                updateRight(self.form).then(result => {      
                     if(result.code!="200"){
                        alert(result.message);
                        return;
                     }              
                     self.reload();
                     self.restForm();
                     
                });
            }
       });
       

      }
  }
}
</script>


