import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Login from '@/components/Login'
import home from '@/components/home'
import companylist from '@/components/views/p2p/company/companylist'
import companyaudit from '@/components/views/p2p/company/companyaudit'
import assetcompany from '@/components/views/p2p/asset/assetcompany'
import foundcompany from '@/components/views/p2p/found/foundcompany'
import foundcompanypool from '@/components/views/p2p/found/foundcompanypool'
import myfoundcompany from '@/components/views/p2p/found/myfoundcompany'

import assetcompanypool from '@/components/views/p2p/asset/assetcompanypool'
import assetitempool from '@/components/views/p2p/asset/assetitempool'
import myassetcompany from '@/components/views/p2p/asset/myassetcompany'
import companyuser from '@/components/views/p2p/company/companyuser'
import usermanage from '@/components/views/p2p/company/usermanage'
import companyrelation from '@/components/views/p2p/report/companyrelation'

import employee from '@/components/views/p2p/user/employee'
import role from '@/components/views/p2p/user/role'
import right from '@/components/views/p2p/user/right'
import user from '@/components/views/p2p/user/user'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: Login
    },
    {
      path: '/Account/Login',
      name: 'Login',
      component: Login
    },
    {
      path: '/home',
      name: 'home',
      component: home,
      children:[
        {
          path:'p2p/company/companylist',
          name:"companylist",
          component:companylist
        },
        {
          path:'p2p/company/companyaudit',
          name:"companyaudit",
          component:companyaudit
        },
        {
          path:'p2p/asset/assetcompany',
          name:"assetcompany",
          component:assetcompany
        },
        {
          path:'p2p/found/foundcompany',
          name:"foundcompany",
          component:foundcompany
        },
        {
          path:'p2p/found/foundcompanypool',
          name:"foundcompanypool",
          component:foundcompanypool
        },
        {
          path:'p2p/found/myfoundcompany',
          name:"myfoundcompany",
          component:myfoundcompany
        },
        {
          path:'p2p/asset/assetcompanypool',
          name:"assetcompanypool",
          component:assetcompanypool
        },
        {
          path:'p2p/asset/assetitempool',
          name:"assetitempool",
          component:assetitempool
        },
        {
          path:'p2p/asset/myassetcompany',
          name:"myassetcompanyp",
          component:myassetcompany
        },
        {
          path:'p2p/company/companyuser',
          name:"companyuser",
          component:companyuser
        },
        {
          path:'p2p/company/usermanage',
          name:"usermanage",
          component:usermanage
        },
        {
          path:'p2p/report/companyrelation',
          name:"companyrelation",
          component:companyrelation
        },
        {
          path:'p2p/user/employee',
          name:"employee",
          component:employee
        },
        {
          path:'p2p/user/role',
          name:"role",
          component:role
        },
        {
          path:'p2p/user/right',
          name:"right",
          component:right
        },
        {
          path:'p2p/user/user',
          name:"user",
          component:user
        }
      ]
    }

  ]
})



