<template>
<div class="conwarp">
<!-- grid -->
<div class="grid">
    <div class="searchbar">
        <el-row :gutter="20">
            <el-col :span="4"><el-input  placeholder="请输入公司ID"   class="w18"  v-model="criteria.id" ></el-input> </el-col>
            <el-col :span="4">
              <el-select v-model="criteria.assetType" placeholder="资产类别">
                <el-option v-for="item in criteria.aType" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
              </el-select>
            </el-col>
            <el-col :span="4">
              <el-select v-model="criteria.everyDeadline" placeholder="资产期限">
                <el-option v-for="item in criteria.eDeadline" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
              </el-select>
            </el-col>
            <el-col :span="2"><el-button type="primary" icon="el-icon-search" @click="search">查询</el-button></el-col>
            <el-col :span="2"><el-button type="primary" @click="search">加入合作名单</el-button></el-col>
            <el-col :span="2"><el-button type="primary" @click="search">导出</el-button></el-col>
        </el-row>
        <el-row>
            <el-col :span="4"><el-input  placeholder="单笔资产额度"   class="w18" v-model="criteria.everySum" ></el-input> </el-col>
            <el-col :span="4"><el-input  placeholder="公司名称"   class="w18"  v-model="criteria.fullName" ></el-input> </el-col>
            <el-col :span="4"><el-input  placeholder="公司简称"   class="w18" v-model="criteria.abbreviationName" ></el-input> </el-col>

        </el-row>
    </div>
    <div class="gmain">
            <el-table :data="tableData"  ref="multipleTable"  tooltip-effect="dark"  @selection-change="handleSelectionChange" stripe style="width: 100%">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column  prop="id"   label="公司ID" ></el-table-column>
            <el-table-column  prop="abbreviation_name"   label="资产方简称" ></el-table-column>
            <el-table-column  prop="asset_size"   label="资产规模（万元）" ></el-table-column>
            <el-table-column  prop="month_size"   label="月新增规模（万元）" ></el-table-column>
            <el-table-column  prop="every_deadline"   label="单笔资产期限" ></el-table-column>
            <el-table-column  prop="every_sum"   label="单笔资产额度（万元）" ></el-table-column>
            <el-table-column  prop="overdue"   label="逾期情况" ></el-table-column>
            <el-table-column  prop="accept_found"   label="可接受资金成本（年化利率）" ></el-table-column>
            <el-table-column  prop="business_area"   label="业务区域" ></el-table-column>
            <el-table-column  prop="is_performance_insurance"  label="履约险" >
              <template scope="scope">
                <span v-if="scope.row.is_performance_insurance==0"> 无</span>
                <span v-if="scope.row.is_performance_insurance==1" >有</span>
              </template>
            </el-table-column>
            <el-table-column  prop="is_guarantee"  label="担保" >
              <template scope="scope">
                <span v-if="scope.row.is_guarantee==0"> 无</span>
                <span v-if="scope.row.is_guarantee==1">有</span>
              </template>
            </el-table-column>
            <el-table-column label="操作"  >
                 <template slot-scope="scope">
                   <el-button size="small" type="danger"   @click="handleDeleteCooperation(scope.$index, scope.row)">移出合作名单</el-button>
                 </template>
            </el-table-column>
        </el-table>
        <div align="center" class="gpage">
                <el-pagination
                     background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20,30]"
                    :page-size="pagesize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="totalCount">
                </el-pagination>
        </div>
    </div>
</div>
</div>
</template>

<script>
import {getMyAssetCompanyList,deleteCooperation} from '@/api'
import MomentPlugin from '@/libs/MomentPlugin'
export default {
  name: 'companylist',
  data () {
      return{
             tableData:[],
             //分页-数量
             pagesize: 10,
             //分页-页码
             currentPage: 1,
             //分页-总数
             totalCount: 100,
             //搜索条件
             criteria: {
                 id:'',
                 assetType:'',
                 aType:[
                   {value: '', label: '全部'},
                   {value: '10', label: '消费分期'},
                   {value: '11', label: '融资租赁'},
                   {value: '12', label: '车抵贷'},
                   {value: '13', label: '房抵贷'},
                   {value: '14', label: '信用贷'},
                   {value: '15', label: '供应链金融'},
                   {value: '16', label: '其他'}
                 ],
                 everyDeadline:'',
                 eDeadline:[
                   {value: '', label: '全部'},
                   {value: '1', label: '0~3个月'},
                   {value: '2', label: '0~12个月'},
                   {value: '3', label: '0~24个月'},
                   {value: '4', label: '24个月以上'},
                 ],
                 everySum:'',
                 fullName:'',
                 abbreviationName:'',
                 businessArea:''
             },
             multipleSelection: [],
             dialogFormVisible: false,
             dialogWidth:'500px',
             formLabelWidth: '80px'

      }
  },
  mounted(){
     var self=this;
     self.reload();
  },
  methods: {
    //分页数据加载
    loadData: function (criteria, pageNum, pagesize) {
      var self = this;
      var params = {page: pageNum, size: pagesize, search: ""};
      if (criteria != null) {
        params.search=JSON.stringify({
          id:criteria.id,
          asset_type:criteria.assetType,
          every_deadline:criteria.everyDeadline,
          every_sum:criteria.everySum,
          business_area:criteria.businessArea,
          full_name:criteria.fullName,
          abbreviation_name:criteria.abbreviationName
        });

      }
      getMyAssetCompanyList(params).then(result => {
        var data = result.data;
        self.pagesize = data.pageSize;
        self.totalCount = data.total;
        self.tableData = data.list;
      });
    },
    reload: function () {
      var self = this;
      self.loadData(this.criteria, self.currentPage, self.pagesize);
    },
    restForm: function () {
      var self = this;
      self.form = {id: '', name: '', img: '', useStatus: '0'};
    },
    close: function () {
      var self = this;
      self.restForm();
    },
    //分页大小变更
    handleSizeChange: function (val) {
      this.pagesize = val;
      this.loadData(this.criteria, this.currentPage, this.pagesize);
    },
    //分页变更
    handleCurrentChange: function (val) {
      this.currentPage = val;
      this.loadData(this.criteria, this.currentPage, this.pagesize);
    },

    //复选框
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //查询
    search: function () {
      var self = this;
      console.log("search");
      this.loadData(self.criteria, this.currentPage, this.pagesize);
    },
    //加入合作关系
    handleDeleteCooperation: function(index, row) {
      console.log(row);
      var self=this;
      if(!confirm("你确定要移出合作名单吗？"))return;
      deleteCooperation({targetCompanyId:row.id}).then(result => {
        alert(result.message);
        if(result.code!="200"){
          return;
        }
        self.reload();
      });
    },
  }
}
</script>



