<template>
<div class="conwarp">
<!-- grid -->
<div class="grid">
    <div class="toolbar">
          <el-button type="primary"  v-show="criteria.assetCompanyMenu" icon="el-icon-plus" @click="addPersonItem()" >添加个人项目</el-button>
          <!-- <el-button type="primary"  icon="el-icon-plus" @click="dialogAssetitemFormVisible=true" >添加企业项目</el-button> -->
    </div>
    <div class="searchbar">
        <el-row :gutter="20">
            <el-col :span="4"><el-input  placeholder="项目编号"   class="w18"  v-model="criteria.id" ></el-input> </el-col>
            <el-col :span="4"><el-input  placeholder="借款人"   class="w18"  v-model="criteria.borrower_name" ></el-input> </el-col>
            <el-col :span="4"><el-input  placeholder="资产金额"   class="w18"  v-model="criteria.borrow_amount" ></el-input> </el-col>
            <el-col :span="2"><el-button type="primary" icon="el-icon-search" @click="search">查询</el-button></el-col>
            <el-col :span="2"><el-button type="primary"  v-show="criteria.assetCompanyMenu" @click="search">审核</el-button></el-col>
           <!--
            <el-col :span="2"><el-button type="primary" v-show="criteria.foundCompanyMenu" @click="###">加入购物车</el-button></el-col>
           -->
            <el-col :span="2"><el-button type="primary" @click="handleExport">导出</el-button></el-col>
            <el-col :span="2"><el-button type="primary" @click="handleExportCar">导出购物车</el-button></el-col>
        </el-row>
        <el-row>
            <el-col :span="4">
              <el-select v-model="criteria.borrow_deadline" placeholder="资产期限">
                <el-option v-for="item in criteria.eDeadline" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
              </el-select>
            </el-col>
            <el-col :span="4">
              <el-select v-model="criteria.repay_way" placeholder="还款方式">
                <el-option v-for="item in criteria.eDeadline" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
              </el-select>
            </el-col>
            <el-col :span="4"><el-input  placeholder="利率"   class="w18" v-model="criteria.borrow_rate" ></el-input> </el-col>
            <el-col :span="4">
              <el-select v-model="criteria.asset_info" placeholder="资产类型">
                <el-option v-for="item in criteria.eDeadline" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
              </el-select>
            </el-col>
            <el-col :span="4">
              <el-select v-model="criteria.use_status" placeholder="项目状态">
                <el-option v-for="item in criteria.eDeadline" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
              </el-select>
            </el-col>
          </el-row>
    </div>
    <div class="gmain">
            <el-table :data="tableData"  ref="multipleTable"  tooltip-effect="dark"  @selection-change="handleSelectionChange" stripe style="width: 100%">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column  prop="id"   label="项目编号" ></el-table-column>
            <el-table-column  prop="borrower_name"   label="借款人" ></el-table-column>
            <el-table-column  prop="borrow_amount"   label="金额（元）" ></el-table-column>
            <el-table-column  prop="borrow_deadline"  label="资产期限" >
              <template scope="scope">
                <span v-if="scope.row.borrow_deadline==1"> 0~3个月</span>
                <span v-if="scope.row.borrow_deadline==2"> 0~12个月</span>
                <span v-if="scope.row.borrow_deadline==3"> 0~24个月</span>
                <span v-if="scope.row.borrow_deadline==4"> 24个月以上</span>
              </template>
            </el-table-column>
            <el-table-column  prop="repay_way"  label="还款方式" >
              <template scope="scope">
                <span v-if="scope.row.repay_way==1">到期还本付息</span>
                <span v-if="scope.row.repay_way==2">等额本息</span>
                <span v-if="scope.row.repay_way==3">先息后本</span>
              </template>
            </el-table-column>
          <el-table-column  prop="borrow_rate"   label="利率" ></el-table-column>
          <el-table-column  prop="asset_info"  label="资产类型" >
            <template scope="scope">
              <span v-if="scope.row.asset_info==10">消费分期</span>
              <span v-if="scope.row.asset_info==11">融资租赁</span>
              <span v-if="scope.row.asset_info==12">车抵贷</span>
              <span v-if="scope.row.asset_info==13">房抵贷</span>
              <span v-if="scope.row.asset_info==14">信用贷</span>
              <span v-if="scope.row.asset_info==15">供应链金融</span>
              <span v-if="scope.row.asset_info==16">其他</span>
            </template>
          </el-table-column>
          <el-table-column  prop="use_status"  label="状态" >
            <template scope="scope">
              <span v-if="scope.row.use_status==0">草稿</span>
              <span v-if="scope.row.use_status==1">待审核</span>
              <span v-if="scope.row.use_status==2">审核不通过</span>
              <span v-if="scope.row.use_status==3">审核通过</span>
              <span v-if="scope.row.use_status==4">发标中</span>
              <span v-if="scope.row.use_status==5">满标</span>
            </template>
          </el-table-column>
          <el-table-column label="操作"  >
               <template slot-scope="scope">
                 <!-- <el-button size="small" type="primary" @click="handleDetail(scope.$index, scope.row)">查看详情</el-button> -->
                 <el-button size="small" type="primary" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                 <el-button size="small" type="danger"   v-if="scope.row.use_status==1" v-show="criteria.assetCompanyMenu"  @click="handleAudit(scope.$index, scope.row)">审核</el-button>
                 <el-button size="small" type="danger"  v-show="criteria.foundCompanyMenu" @click="handleAddCar(scope.$index, scope.row)">加入购物车</el-button>
               </template>
          </el-table-column>
        </el-table>
        <div align="center" class="gpage">
                <el-pagination
                     background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20,30]"
                    :page-size="pagesize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="totalCount">
                </el-pagination>
        </div>
    </div>
</div>

    <el-dialog title="借款人个人项目" :visible.sync="dialogAssetitemFormVisible" :before-close="close1" :width="dialogWidth" class="demo-ruleForm">
      <el-form :model="personInfo" :rules="rules" ref="personInfo" label-width="100px">
      <el-tabs type="border-card" v-model="firstCardItemName">
        <el-tab-pane name="baseInfo" label="借款人基础信息">
            <el-form-item label="姓名" prop="name">
                <el-input v-model="personInfo.name"></el-input>
            </el-form-item>
            <el-form-item label="身份证" prop="idNumber">
                <el-input v-model="personInfo.idNumber"></el-input>
            </el-form-item>
            <el-form-item label="手机" prop="mobile">
                <el-input v-model="personInfo.mobile"></el-input>
            </el-form-item>
            <el-form-item label="性别" prop="sex">
                <el-select v-model="personInfo.sex">
                    <el-option v-for="item in sexList" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="住址" prop="address">
                <el-input v-model="personInfo.address"></el-input>
            </el-form-item>
            <el-form-item label="邮箱" prop="email">
                <el-input v-model="personInfo.email"></el-input>
            </el-form-item>
            <el-form-item label="职业" prop="professionType">
                <el-select v-model="personInfo.professionType">
                    <el-option v-for="item in professionTypeList" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="月收入" prop="salary">
                <el-input v-model="personInfo.salary"></el-input>
            </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="项目信息">
              <el-form-item label="项目标题" prop="itemTitle">
                  <el-input v-model="personInfo.itemTitle"></el-input>
              </el-form-item>
              <el-form-item label="资产类型" prop="assetInfo">
                  <el-select v-model="personInfo.assetInfo">
                    <el-option v-for="item in assetInfoList" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
                  </el-select>
              </el-form-item>
              <el-form-item label="金额" prop="borrowAmount">
                  <el-input v-model="personInfo.borrowAmount"></el-input>
              </el-form-item>
              <el-form-item label="期限" prop="borrowDeadline">
                  <el-select v-model="personInfo.borrowDeadline">
                    <el-option v-for="item in borrowDeadlineList" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
                  </el-select>
              </el-form-item>
              <el-form-item label="还款方式" prop="repayWay">
                  <el-select v-model="personInfo.repayWay">
                    <el-option v-for="item in repayWayList" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
                  </el-select>
              </el-form-item>
              <el-form-item label="借款年利率" prop="borrowRate">
                  <el-input v-model="personInfo.borrowRate"></el-input>
              </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="信审、附件材料">
              <el-form-item label="身份证正面" prop="idCardFirstUrl">
                  <img :src="personInfo.idCardFirstUrl" width="20%" alt="front" />
                  <input type="file"  name="file" @change="getFile($event,1)" />
              </el-form-item>
              <el-form-item label="身份证反面" prop="idCardSecondUrl">
                  <img :src="personInfo.idCardSecondUrl" width="20%" alt="front" />
                  <input type="file"  name="file" @change="getFile($event,2)" />
              </el-form-item>
              <el-form-item label="银行卡正面" prop="bankCardFirstUrl">
                  <img :src="personInfo.bankCardFirstUrl" width="20%" alt="front" />
                  <input type="file"  name="file" @change="getFile($event,3)" />
              </el-form-item>
              <el-form-item label="银行卡反面" prop="bankCardSecondUrl">
                  <img :src="personInfo.bankCardSecondUrl" width="20%" alt="front" />
                  <input type="file"  name="file" @change="getFile($event,4)" />
              </el-form-item>
        </el-tab-pane>
      </el-tabs>
      </el-form>
       <div slot="footer" class="dialog-footer">
          <el-button @click="close1()">取 消</el-button>
          <el-button type="primary" v-show="criteria.assetCompanyMenu" @click="toSavePerson()">确 定</el-button>
      </div>
    </el-dialog>

  <el-dialog title="审核" :visible.sync="dialogAuditFormVisible" :before-close="close" :width="dialogAuditWidth" class="demo-ruleForm">
    <el-form :model="audit" :rules="rules" ref="audit" label-width="80px">
      <el-form-item label="审核结果" prop="status">
        <el-select v-model="audit.status" placeholder="请选择">
          <el-option label="审核不通过" value="2"></el-option>
          <el-option label="审核通过" value="3"></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="close()">取 消</el-button>
      <el-button type="primary" @click="toAudit()">确 定</el-button>
    </div>
  </el-dialog>

</div>
</template>

<script>
import {getAssetItemPoolList,auditAssetItem,uploadFile,addPersonItem,itemDetail,addCar,exportAssetItem,exportAssetItemCar} from '@/api'
import {idcardRule,mobileRule,emailRule,decimal_10_2,number_11_null} from '@/libs/Regular'
import MomentPlugin from '@/libs/MomentPlugin'
export default {
  name: 'assetitem',
  data () {
      return{
             tableData:[],
             //分页-数量
             pagesize: 10,
             //分页-页码
             currentPage: 1,
             //分页-总数
             totalCount: 100,
             firstCardItemName:'baseInfo',
             //搜索条件
             criteria: {
                 id:'',
                 borrower_name:'',
                 borrow_amount:'',
                 borrow_deadline:'',
                 repay_way:'',
                 borrow_rate:'',
                 asset_info:'',
                 use_status:'',
                 bDeadline:[
                   {value: '', label: '全部'},
                   {value: '1', label: '0~3个月'},
                   {value: '2', label: '0~12个月'},
                   {value: '3', label: '0~24个月'},
                   {value: '4', label: '24个月以上'}
                 ],
                 rWay:[
                   {value: '', label: '全部'},
                   {value: '1', label: '到期还本付息'},
                   {value: '2', label: '等额本息'},
                   {value: '3', label: '先息后本'}
                 ],

                 aInfo:[
                   {value: '11', label: '融资租赁'},
                   {value: '12', label: '车抵贷'},
                   {value: '13', label: '房抵贷'},
                   {value: '14', label: '信用贷'},
                   {value: '15', label: '供应链金融'},
                   {value: '16', label: '其他'},
                   {value: '10', label: '消费分期'},
                 ],
                 uStatus:[
                   {value: '', label: '全部'},
                   {value: '0', label: '草稿'},
                   {value: '1', label: '待审核'},
                   {value: '2', label: '审核不通过'},
                   {value: '3', label: '审核通过'},
                   {value: '4', label: '发标中'},
                   {value: '5', label: '满标'}
                 ],
               },
               personInfo:{
                  itemId:'',

                  name:'',
                  idNumber:'',
                  mobile:'',
                  sex:'',
                  address:'',
                  email:'',
                  professionType:'',
                  salary:'',

                  itemTitle:'',
                  assetInfo:'',
                  borrowAmount:'',
                  borrowDeadline:'',
                  repayWay:'',
                  borrowRate:'',

                  idCardFirstUrl:'',
                  idCardSecondUrl:'',
                  bankCardFirstUrl:'',
                  bankCardSecondUrl:'',
               },
               sexList: [
                     {value: '', label: '请选择'},
                     {value: '0', label: '男'},
                     {value: '1', label: '女'}
                ],
                professionTypeList: [
                     {value: '', label: '请选择'},
                     {value: '1', label: '企业主'},
                     {value: '2', label: '个体工商户'},
                     {value: '3', label: '上班人群'},
                     {value: '4', label: '学生'},
                     {value: '5', label: '无固定职业'},
                ],
                assetInfoList:[
                     {value: '', label: '请选择'},
                     {value: '10', label: '消费分期'},
                     {value: '11', label: '融资租赁'},
                     {value: '12', label: '车抵贷'},
                     {value: '13', label: '房抵贷'},
                     {value: '14', label: '信用贷'},
                     {value: '15', label: '供应链金融'},
                     {value: '16', label: '其他'},
                ],
                borrowDeadlineList:[
                     {value: '', label: '请选择'},
                     {value: '1', label: '0~3个月'},
                     {value: '2', label: '0~12个月'},
                     {value: '3', label: '0~24个月'},
                     {value: '4', label: '24个月以上'},
                ],
                repayWayList:[
                     {value: '', label: '请选择'},
                     {value: '1', label: '到期还本付息'},
                     {value: '2', label: '等额本息'},
                     {value: '3', label: '先息后本'},
                ],
                rules:{
                  name: [{max: 20,required: true, message: '请填写姓名，长度在20个字符以内', trigger: 'blur' }],
                  idNumber: [{validator:idcardRule,required: true,trigger: 'blur' }],
                  mobile: [{validator:mobileRule,required: true, trigger: 'blur' }],
                  sex: [{required: true, message: '请选择性别', trigger: 'blur' }],
                  address: [{max: 100,required: true, message: '请填写地址，长度在100个字符以内', trigger: 'blur' }],
                  email: [{validator:emailRule,required: true, trigger: 'blur' }],
                  salary: [{validator:number_11_null,message: '请输入整数', trigger: 'blur' }],

                  itemTitle: [{max: 20,required: true, message: '请填写项目标题，长度在20个字符以内', trigger: 'blur' }],
                  assetInfo: [{required: true, message: '请选择资产类型', trigger: 'blur' }],
                  borrowAmount: [{validator:decimal_10_2,required: true,trigger: 'blur' }],
                  borrowDeadline: [{required: true, message: '请选择期限', trigger: 'blur' }],
                  repayWay: [{required: true, message: '请选择还款方式', trigger: 'blur' }],
                  borrowRate: [{validator:decimal_10_2,required: true,trigger: 'blur' }],

                  idCardFirstUrl: [{required: true, message: '请选择身份证正面图片', trigger: 'blur' }],
                  idCardSecondUrl: [{required: true, message: '请选择身份证反面图片', trigger: 'blur' }],
                  bankCardFirstUrl: [{required: true, message: '请选择银行卡正面图片', trigger: 'blur' }],
                  bankCardSecondUrl: [{required: true, message: '请选择银行卡反面图片', trigger: 'blur' }],
               },
               dialogAssetitemFormVisible: false,
               multipleSelection: [],
               dialogWidth:'700px',
               assetCompanyMenu:false,
               foundCompanyMenu:false,
               dialogAuditFormVisible: false,
               dialogWidth:'700px',
               dialogAuditWidth:'400px',
               audit:{
                  status:'',
                  id:'',
               },
               multipleSelection: []
      }
  },
  mounted(){
    var self=this;
    var userType = sessionStorage.userType;
    if(userType > 1){
      self.criteria.assetCompanyMenu = false;
      self.criteria.foundCompanyMenu = true;
    }else{//资产方
      self.criteria.assetCompanyMenu = true;
      self.criteria.foundCompanyMenu = false;
    }
    self.reload();

  },
  methods: {
    //分页数据加载
    loadData: function (criteria, pageNum, pagesize) {
      var self = this;
      var params = {page: pageNum, size: pagesize, search: ""};
      if (criteria != null) {
        params.search=JSON.stringify({
          id:criteria.id,
          borrower_name:criteria.borrower_name,
          borrow_amount:criteria.borrow_amount,
          borrow_deadline:criteria.borrow_deadline,
          repay_way:criteria.repay_way,
          borrow_rate:criteria.borrow_rate,
          asset_info:criteria.asset_info,
          use_status:criteria.use_status
        });

      }
      getAssetItemPoolList(params).then(result => {
        var data = result.data;
        self.pagesize = data.pageSize;
        self.totalCount = data.total;
        self.tableData = data.list;
      });
    },
    reload: function () {
      var self = this;
      self.loadData(null, self.currentPage, self.pagesize);
    },
    restForm:function(){
        var self=this;
        if (self.$refs['personInfo']!==undefined) {
            self.$refs['personInfo'].resetFields();

            self.personInfo = {
                  itemId:'',

                  name:'',
                  idNumber:'',
                  mobile:'',
                  sex:'',
                  address:'',
                  email:'',
                  professionType:'',
                  salary:'',

                  itemTitle:'',
                  assetInfo:'',
                  borrowAmount:'',
                  borrowDeadline:'',
                  repayWay:'',
                  borrowRate:'',

                  idCardFirstUrl:'',
                  idCardSecondUrl:'',
                  bankCardFirstUrl:'',
                  bankCardSecondUrl:'',
               }
        }
        self.dialogAssetitemFormVisible = false;
     },
    close: function () {
      var self = this;
      self.dialogAuditFormVisible = false;
    },
    close1: function () {
      var self = this;
      self.restForm();
    },
    //分页大小变更
    handleSizeChange: function (val) {
      this.pagesize = val;
      this.loadData(this.criteria, this.currentPage, this.pagesize);
    },
    //分页变更
    handleCurrentChange: function (val) {
      this.currentPage = val;
      this.loadData(this.criteria, this.currentPage, this.pagesize);
    },

    //复选框
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //查询
    search: function () {
      var self = this;
      console.log("search");
      this.loadData(self.criteria, this.currentPage, this.pagesize);
    },
    handleAudit:function(index, row) {
      var self=this;
      self.audit.id = row.id;
      self.dialogAuditFormVisible = true;
    },
    toAudit:function(){
      var self=this;
      auditAssetItem({id:self.audit.id,status:self.audit.status}).then(result => {
        if(result.code!="200"){
          this.$message.error(result.message);
          return;
        }
        this.$message({type: 'success',message: '审核成功!'});
        self.dialogAuditFormVisible=false;
        self.reload();
      });
    },

    //加入合作关系
    handleAddCar: function(index, row) {
     var self=this;
      if(!confirm("你确定要加入购物车吗？"))return;
      addCar({id:row.id}).then(result => {
        alert(result.message);
        if(result.code!="200"){
          return;
        }
        self.reload();
      });
    },
    addPersonItem:function(){
      var self = this;
       self.firstCardItemName = 'baseInfo';
       self.dialogAssetitemFormVisible=true;
    }
    ,
    getFile(event,typeNum) {
         var file = event.target.files[0];
        if(file != undefined){
            this.uploadFile(file,typeNum,event);
            event.target.value = '';
         }
     },
     uploadFile(file,typeNum,event){
         event.preventDefault();
         var self=this;
         let formData = new FormData();
         formData.append("file",file);
         uploadFile({formData:formData,callback:function(result){
            console.log("uploadFile",result);
             if(result.code!="200"){
                this.$message.error(result.message);
                return;
             }
             if(typeNum == 1){
                self.personInfo.idCardFirstUrl=result.data.url;
             }
             else if(typeNum == 2){
                self.personInfo.idCardSecondUrl=result.data.url;
             }
             else if(typeNum == 3){
                self.personInfo.bankCardFirstUrl=result.data.url;
             }
             else if(typeNum == 4){
                self.personInfo.bankCardSecondUrl=result.data.url;
             }
        }});
      },
      toSavePerson: function () {
       var self = this;
       var formName = 'personInfo';
      self.$refs[formName].validate((valid) => {
        if(valid){
          var params = self.personInfo;

          addPersonItem(params).then(result => {
            var data = result.data;
            if(result.code!="200"){
                  this.$message.error(result.message);
                  return;
              }
            this.$message({type: 'success',message: '保存成功!'});
            self.reload();
            self.restForm();
          });
        }

      });

    },

    handleDetail: function(index, row){

    },
    export:function(criteria,exportType){//导出表格
      var self = this;
      var params = {search: ""};
      if (criteria != null && exportType == "1") {
        params.search=JSON.stringify({
          id:criteria.id,
          borrower_name:criteria.borrower_name,
          borrow_amount:criteria.borrow_amount,
          borrow_deadline:criteria.borrow_deadline,
          repay_way:criteria.repay_way,
          borrow_rate:criteria.borrow_rate,
          asset_info:criteria.asset_info,
          use_status:criteria.use_status
        });

      }
      var form = document.createElement('form');
      form.name = 'form'
      document.body.appendChild(form);
      for(var obj in params) {
        if(params.hasOwnProperty(obj)) {
          var input = document.createElement('input');
          input.tpye='hidden';
          input.name = obj;
          input.value = params[obj];
          form.appendChild(input)
        }
      }
      form.method = "post";//请求方式                                            ?access_token=
      //导出当前报表
      if(exportType == "1"){
        form.action = exportAssetItem();
      }
      //导出购物车
      if(exportType == "2"){
        form.action = exportAssetItemCar();
      }

      form.submit();
      document.body.removeChild(form);
    },
    handleExport: function () {
      var self = this;
      this.export(self.criteria,"1");
    },
    handleExportCar: function () {
      var self = this;
      this.export(self.criteria,"2");
    },
    handleEdit: function(index, row){
       var self = this;
        self.personInfo.itemId = row.id;

       itemDetail({id:row.id}).then(result => {
          var data = result.data;

          self.personInfo.name = data.name;
          self.personInfo.idNumber = data.id_number;
          self.personInfo.mobile = data.mobile;
          self.personInfo.sex = data.sex + '';
          self.personInfo.address = data.address;
          self.personInfo.email = data.email;
          self.personInfo.professionType = data.profession_type + '';
          self.personInfo.salary = data.salary;

          self.personInfo.itemTitle = data.item_title;
          self.personInfo.assetInfo = data.asset_info  + '';
          self.personInfo.borrowAmount = data.borrow_amount;
          self.personInfo.borrowDeadline = data.borrow_deadline  + '';
          self.personInfo.repayWay = data.repay_way  + '';
          self.personInfo.borrowRate = data.borrow_rate;

          self.personInfo.idCardFirstUrl = data.id_card_first_url;
          self.personInfo.idCardSecondUrl = data.id_card_second_url;
          self.personInfo.bankCardFirstUrl = data.bank_card_first_url;
          self.personInfo.bankCardSecondUrl = data.bank_card_second_url;

          self.addPersonItem();

      });


    },
  }
}
</script>



