<template>
<div class="conwarp">
<!-- grid -->
<div class="grid">
    <div class="searchbar">
        <el-row :gutter="22">
            <el-col :span="4"><el-input  placeholder="资产方ID"   class="w18"  v-model="criteria.assetId" ></el-input> </el-col>
            <el-col :span="4"><el-input  placeholder="资产方简称"   class="w18"  v-model="criteria.assetName" ></el-input> </el-col>
            <el-col :span="4"><el-input  placeholder="资金方ID"   class="w18"  v-model="criteria.foundId" ></el-input> </el-col>
            <el-col :span="4"><el-input  placeholder="资金方简称"   class="w18"  v-model="criteria.foundName" ></el-input> </el-col>

            <el-col :span="2"><el-button type="primary" icon="el-icon-search" @click="search">查询</el-button></el-col>
            <el-col :span="2"><el-button type="primary" @click="handleExport">导出</el-button></el-col>
        </el-row>

    </div>
    <div class="gmain">
            <el-table :data="tableData"  ref="multipleTable"  tooltip-effect="dark"  @selection-change="handleSelectionChange" stripe style="width: 100%">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column  prop="asset_id"   label="资产方ID" ></el-table-column>
            <el-table-column  prop="asset_name"   label="资产方简称" ></el-table-column>
            <el-table-column  prop="found_id"   label="资金方ID" ></el-table-column>
            <el-table-column  prop="found_name"   label="资金方简称" ></el-table-column>
        </el-table>
        <div align="center" class="gpage">
                <el-pagination
                     background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20,30]"
                    :page-size="pagesize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="totalCount">
                </el-pagination>
        </div>
    </div>
</div>
</div>
</template>

<script>
import {getAssetFoundRelationList,exportAssetFoundRelation} from '@/api'
import MomentPlugin from '@/libs/MomentPlugin'
export default {
  name: 'companylist',
  data () {
      return{
             tableData:[],
             //分页-数量
             pagesize: 10,
             //分页-页码
             currentPage: 1,
             //分页-总数
             totalCount: 100,
             //搜索条件
             criteria: {
               assetId:'',
               assetName:'',
               foundId:'',
               foundName:''
             },
             multipleSelection: [],
             dialogFormVisible: false,
             dialogWidth:'500px',
             formLabelWidth: '80px'

      }
  },
  mounted(){
     var self=this;
     self.reload();
  },
  methods: {
    //分页数据加载
    loadData: function (criteria, pageNum, pagesize) {
      var self = this;
      var params = {page: pageNum, size: pagesize, search: ""};
      if (criteria != null) {
        params.search=JSON.stringify({
          assetId:criteria.assetId,
          assetName:criteria.assetName,
          foundId:criteria.foundId,
          foundName:criteria.foundName,
        });
      }
      getAssetFoundRelationList(params).then(result => {
        var data = result.data;
        self.pagesize = data.pageSize;
        self.totalCount = data.total;
        self.tableData = data.list;
      });
    },
    ////////////////////////////////////
    exportAssetFoundRelation:function(criteria){//导出表格
      var self = this;
      var params = {search: ""};
      if (criteria != null) {
        params.search=JSON.stringify({
          assetId:criteria.assetId,
          assetName:criteria.assetName,
          foundId:criteria.foundId,
          foundName:criteria.foundName,
        });
      }
      var form = document.createElement('form');
      form.name = 'form'
      document.body.appendChild(form);
      for(var obj in params) {
        if(params.hasOwnProperty(obj)) {
          var input = document.createElement('input');
          input.tpye='hidden';
          input.name = obj;
          input.value = params[obj];
          form.appendChild(input)
        }
      }
      form.method = "post";//请求方式                                            ?access_token=
      form.action = exportAssetFoundRelation();
      form.submit();
      document.body.removeChild(form);

    },
   ////////////////////////////////////
    reload: function () {
      var self = this;
      self.loadData(this.criteria, self.currentPage, self.pagesize);
    },
    close: function () {
      var self = this;
      self.restForm();
    },
    //分页大小变更
    handleSizeChange: function (val) {
      this.pagesize = val;
      this.loadData(this.criteria, this.currentPage, this.pagesize);
    },
    //分页变更
    handleCurrentChange: function (val) {
      this.currentPage = val;
      this.loadData(this.criteria, this.currentPage, this.pagesize);
    },

    //复选框
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //查询
    search: function () {
      var self = this;
      this.loadData(self.criteria, this.currentPage, this.pagesize);
    },
    handleExport: function () {
      var self = this;
      this.exportAssetFoundRela(self.criteria);
    }
  }
}
</script>



