<template>
<div class="conwarp">
<!-- grid -->
<div class="grid">
    <div class="toolbar">
        <el-button type="primary"  icon="el-icon-plus" @click="dialogFormVisible=true" >添加</el-button>
    </div>
    <div class="searchbar">
        <el-row>
            <el-col :span="4"><el-input  placeholder="请输入名称"   class="w18"  prefix-icon="el-icon-search" v-model="criteria.roleName" ></el-input> </el-col>          
            <el-col :span="4">  
                <el-select v-model="criteria.userStatus" placeholder="角色状态">
                    <el-option v-for="item in criteria.status" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
                 </el-select>
            </el-col>
            <el-col :span="4"><el-button type="primary" icon="el-icon-search" @click="search">搜索</el-button></el-col>
            <el-col :span="12"></el-col>
        </el-row>               
    </div>
    <div class="gmain">
            <el-table :data="tableData"  ref="multipleTable"  tooltip-effect="dark"  @selection-change="handleSelectionChange" stripe style="width: 100%">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column  prop="id"   label="编号" ></el-table-column>
            <el-table-column  prop="parentId"   label="父编号" ></el-table-column>
            <el-table-column  prop="roleName"   label="角色名称" ></el-table-column>
            <el-table-column  prop="userStatus"   label="角色状态">
                <template scope="scope">
                    <span v-if="scope.row.userStatus==0" style="color:green;" >启用</span>
                    <span v-if="scope.row.userStatus==1" style="color:red;" >关闭</span>                
                </template>
            </el-table-column>
            <el-table-column  prop="description"   label="角色描述" ></el-table-column>
            <el-table-column label="操作"  > 
                 <template slot-scope="scope">
                    <el-button size="small" type="primary"  @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button size="small" type="danger"   @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    <el-button size="small" type="success"   @click="rolePermision(scope.$index, scope.row)">权限</el-button>
                 </template>
            </el-table-column>
        </el-table>
        <div align="center" class="gpage">
                <el-pagination
                     background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20,30]"
                    :page-size="pagesize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="totalCount">
                </el-pagination>
        </div>
    </div>
</div>
<!-- form -->
<el-dialog title="角色信息" :visible.sync="dialogFormVisible" :before-close="close" :width="dialogWidth">
    <el-form :model="form" :rules="rules" ref="form">
        <el-form-item label="角色名称" :label-width="formLabelWidth" prop="roleName">
             <input type="hidden" v-model="form.id"/>
            <el-input v-model="form.roleName" auto-complete="off" size="small"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" :label-width="formLabelWidth" prop="description">
            <el-input v-model="form.description" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="父编号" :label-width="formLabelWidth" prop="parentId">
            <el-input v-model="form.parentId" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="角色状态" :label-width="formLabelWidth">
            <el-select v-model="form.userStatus" placeholder="请选择应用状态">
                <el-option label="启用" value="0"></el-option>
                <el-option label="关闭" value="1"></el-option>
            </el-select>
        </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible=false">取 消</el-button>
        <el-button type="primary" @click="formSave('form')">确 定</el-button>
    </div>

</el-dialog>
<!--角色权限-->
<el-dialog title="角色权限设置" :visible.sync="dialogPermisionVisible" :before-close="roleClose" :width="dialogRoleWidth"  >
<el-row>
  <el-col :span="24" style="text-align:right;margin-bottom: 16px">
     <el-button type="success"   @click="savePermissions" >保存权限</el-button>
  </el-col>
</el-row>
<el-row :gutter="20">

     <el-tree
        :data="treeData"
        show-checkbox
        default-expand-all
        node-key="id"
        ref="tree"
        highlight-current
        :props="defaultProps"        
        >
    </el-tree>   
</el-row>

</el-dialog>
</div>
</template>

<script>
import {addRole,delRole,updateRole,getRoleList,getPermission,getRolePermission,savePermission} from '@/api'

export default {
  name: 'rolemanage',
  data () {
      return{
             tableData:[],
             tableRoleData:[],
             treeData:[],
            defaultProps: {
                children: 'children',
                label: 'label'
             },
             roleCode:"",
             //分页-数量
             pagesize: 10,
             //分页-页码
             currentPage: 1,
             //分页-总数
             totalCount: 100,
             //搜索条件
             criteria: {
                 appName:'',
                 userStatus:'',
                 status:[
                     {value: '', label: '所有状态'},
                     {value: '0', label: '启用'},
                     {value: '1', label: '关闭'}
                 ]
             },
            criteriaRole: {
                 appName:'',
                 appCode:'',
                 routeType:'',
                 routeTypes:[
                     {value: '', label: '所有类型'},
                     {value: '0', label: '根路由'},
                     {value: '1', label: '普通路由'}
                 ],
                 routeStatus:'',
                 status:[
                     {value: '', label: '所有状态'},
                     {value: '0', label: '启用'},
                     {value: '1', label: '关闭'}
                 ],
                 routeMethod:'',
                 routeMethods:[
                     {value: '', label: '所有方式'},
                     {value: '0', label: '菜单'},
                     {value: '1', label: '页面'},
                     {value: '2', label: 'API'},
                     {value: '3', label: '资源'}
                 ],
                 appList:[]
             },
             multipleSelection: [],
             multipleRoleSelection: [],
             hasPermision:[],
             form:{
                 id:'',                
                 roleName:'',
                 userStatus:'0',
                 roleFlag:''      
             },
             rules:{
                roleName:[
                     { required: true, message: '请输入角色名称', trigger: 'blur' },
                     { min: 2, max:25, message: '长度在 2 到 25 个字符', trigger: 'blur' }
                    ],
                roleFlag:[ 
                    { required: true, message: '请输入角色标签', trigger: 'blur' },
                     {  max:20, message: '长度限制为20 个字符', trigger: 'blur' }
                    ]
             },
             dialogFormVisible: false,
             dialogWidth:'400px',
             formLabelWidth: '80px',
             dialogPermisionVisible:false,
             dialogRoleWidth:'600px',
             dialogRoleHeight:'600px'
             
      }
  },
  mounted(){
     var self=this;
     self.reload();

  },
  methods:{
     //分页数据加载
     loadData: function(criteria, pageNum, pagesize){ 
        var self=this;
        var params={page:pageNum,size:pagesize,search:""};
        if(criteria!=null){
            params.search=JSON.stringify({roleName:criteria.roleName,userStatus:criteria.userStatus});    
        }
        getRoleList(params).then(result => {
                var data=result.data;
                self.pagesize=data.pageSize;
                self.totalCount=data.total;
                self.tableData=data.list;
        });
     },
     reload:function(){
         var self=this;
          self.loadData(null,self.currentPage,self.pagesize);
     },
     restForm:function(){
        var self=this;
        self.form={id:'',roleName:'',userStatus:'0',roleFlag:''};
        self.dialogFormVisible=false;
     },
     close:function(){
         var self=this;
         self.restForm();
     },
     roleClose:function(){
          var self=this;
          self.roleCode="";
          self.treeData=[];
          self.dialogPermisionVisible=false;
     },
     //分页大小变更
     handleSizeChange: function(val) {
        this.pagesize = val;
        this.loadData(this.criteria, this.currentPage, this.pagesize);
     },
     //分页变更
     handleCurrentChange: function(val) {
        this.currentPage = val;
        this.loadData(this.criteria, this.currentPage, this.pagesize);
     },
     handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      //查询
      search:function(){
          var self=this;
          this.loadData(self.criteria, this.currentPage, this.pagesize);
      },
      //加载权限
      loadPermision:function(){
          var self=this;              
         getPermission({roleCode:self.roleCode}).then(result => {
              self.treeData=self.menuToItems(result.data);     
              self.showPermision();      
          });
      },
      isNull(el){
        return typeof(el) == "undefined" ||el==null || el=="";
      },
       /**
       * 递归菜单数据
       */
      menuToItems(menu){
         const self = this;
         if(self.isNull(menu)){
           return [];
         }
         var eachFn=function(pid,data){
           var jitems=[];
           for(var i=0;i<data.length;i++){
             var npid=menu[i]["parentId"];
             if(npid==pid){
                var jitem={};
                jitem.label=menu[i].rightName;
                jitem.id=menu[i].id;
                jitem.children=eachFn(menu[i]["id"],menu);
                jitems.push(jitem);
             }
           }
           return jitems;
         };
         var items=[];
         for(var i=0;i<menu.length;i++){
             var pid=menu[i]["parentId"];
             if(self.isNull(pid)){
               var item={};
               item.label=menu[i].rightName;
               item.id=menu[i].id;
               item.children=eachFn(menu[i]["id"],menu);
               items.push(item);
             }
          }
       return items;
      },
      //显示已选角色权限
      showPermision:function(){
        var self=this;          
        getRolePermission({roleId:self.roleCode}).then(result => {
            if(result.code!=200){alert(result.message); return;}
            console.log(result);
            self.$refs.tree.setCheckedKeys(result.data);
         });
      },
      //设置角色权限
      rolePermision:function(index,row){
         var self=this;
         self.roleCode=row.id;
         self.dialogPermisionVisible=true;
         self.loadPermision();
      
      },
      //保存角色权限
      savePermissions:function(){
        var  self=this;
        var params={roleId:self.roleCode,ids:""};      
        var ids="";
        self.$refs.tree.getCheckedNodes().forEach(p=>{ ids+=p.id+"," });
        if(ids.length>0)ids=ids.substring(0,ids.length-1);
        params.ids=ids;
        savePermission(params).then(result => {
            if(result.code==200){
                alert("保存成功!");
            }
        });
      },
    //编辑
     handleEdit: function(index, row) {
        var self=this;
        var detail=self.form;
        detail.id=row.id;
        detail.parentId=row.parentId;
        detail.roleName=row.roleName;
        detail.userStatus=row.userStatus.toString();
        detail.description=row.description;
        self.dialogFormVisible=true;
     },
     //删除
     handleDelete: function(index, row) {
          var self=this;
          if(!confirm("你确定要删除吗？"))return;
          delRole({id:row.id}).then(result => {  
             if(result.code!="200"){
                alert(result.message);
                return;
             }        
             self.reload();  
          });
     },
     //保存表单
     formSave(formName){
        var self=this;
        self.$refs[formName].validate((valid) => {
            if(!valid)return;
          
            if(self.form.id==""){
                //add
                addRole(self.form).then(result => {
                    if(result.code!="200"){
                        alert(result.message);
                        return;
                    }                  
                    self.reload();
                    self.restForm();
                   
                });
            }else{
                //update
                updateRole(self.form).then(result => {      
                     if(result.code!="200"){
                        alert(result.message);
                        return;
                     }              
                     self.reload();
                     self.restForm();
                     
                });
            }
       });
       

      }
  }
}
</script>


